import React, { useState } from "react";
import { Button, Grid, TextField, Box, InputLabel, Stack, MenuItem, Select, FormControl } from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { CustomButton } from "../../../../genericComponents/customButton";
import { useAppDispatch, useAppSelector } from "../../../../assets/hooks";
import { editProfile } from "../../../../services";
import { setfamilyInfo } from "../../../../store/Slices/familyInfoSlice";
import { setPopup } from "../../../../store/Slices/popupSlice";
import { secondaryButton } from "../../../../components/styles/customStyles";
import { Formik, Field, FieldArray, ErrorMessage } from "formik";
import * as Yup from "yup";
import ReactPhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/style.css";
import Images from "src/assets";
import parsePhoneNumberFromString from "libphonenumber-js";

const validationSchema = Yup.object({
  familyDetails: Yup.array()
    .of(
      Yup.object({
        GuardianFirstName: Yup.string()
        .max(50, "First name cannot exceed 50 characters")
        .required('First name is required'),
        GuardianLastName: Yup.string()
        .max(50, "Last name cannot exceed 50 characters")
        .required('Last name is required'),
        GuardianOccupation: Yup.string().required("Occupation is required"),
        RelationshipWithStudent: Yup.string().required("Relationship is required"),
        GuardianContactNumber: Yup.object({
          number:Yup.string()
          .required("Phone number is required")
          .test("is-valid-phone", "Invalid phone number", function (value) {
            const { countryCode } = this.parent;
            if (!countryCode || !value) return false; // Ensure both countryCode and number are provided
  
            try {
              const phoneNumber = parsePhoneNumberFromString(`${countryCode}${value}`, countryCode);
              return phoneNumber?.isValid();
            } catch (error) {
              return false;
            }
          }),
          countryCode: Yup.string().required("Country code is required"),
        }).required("Phone number is required"),
      })
    )
    .required("Must have family members")
    .min(1, "Minimum of 1 family member is required"),
});

// Helper: always keep at least one entry, but allow deleting any index
const getNewFamilyArrayAfterDelete = (arr: any[], idx: number) => {
  if (arr.length === 1) {
    return [{
      GuardianFirstName: "",
      GuardianLastName: "",
      GuardianEmail: "",
      GuardianOccupation: "",
      GuardianQualification: "",
      RelationshipWithStudent: "",
      GuardianContactNumber: { number: "", countryCode: "" },
    }];
  }
  const newArr = arr.slice();
  newArr.splice(idx, 1);
  return newArr;
};

const Family = () => {
  const familyData = useAppSelector((state) => state.familyInfo);
  const dispatch = useAppDispatch();
  const [loading, setLoading] = useState(false);
  const [formEdit, setFormEdit] = useState(false);

  const initialValues = {
    familyDetails:
      familyData.data?.length > 0
        ? familyData.data
        : [
          {
            GuardianFirstName: "",
            GuardianLastName: "",
            GuardianEmail: "",
            GuardianOccupation: "",
            GuardianQualification: "",
            RelationshipWithStudent: "",
            GuardianContactNumber: {
              number: "",
              countryCode: ""
            },
          },
        ],
  };

  const submitForm = async (values: any) => {
    let family = {
      familyDetails: values.familyDetails,
    };
    try {
      setLoading(true)
      const response = await editProfile(family);
      if (response) {
        dispatch(setfamilyInfo(response.data.data.familyDetails));
        dispatch(
          setPopup({
            show: true,
            data: {
              container: {
                name: "success",
                data: "Family details added successfully",
                dimensions: {
                  width: "30%",
                  height: "40%",
                  show: "none"
                },
              },
              type: "custom",
            },
          })
        );
      }
      setLoading(false)
    } catch (error) { 
      setLoading(false)
    }
  };

  return (
    <>
      {formEdit ? (
        <Box sx={{ overflowY: "scroll", px: 3, height: "75vh" }}>
          <Formik
            initialValues={initialValues}
            validationSchema={validationSchema}
            onSubmit={submitForm}
          >
            {({ values, handleChange, handleBlur, handleSubmit, setFieldValue }) => (
              <form onSubmit={handleSubmit}>
                <FieldArray name="familyDetails">
                  {({ push, remove }) => (
                    <div>
                      {values.familyDetails.map((familyMember, index) => (
                        <div className="mb-5" key={index}>
                          <Grid container spacing={4} sx={{ px: 2 }}>
                            {/* RelationshipWithStudent */}
                            <Grid item xs={12} sm={6}>
                              <FormControl fullWidth variant="outlined" size="small" sx={{ mt: 1,color:"#8E9091"}}>
                                <InputLabel id="family">
                                  Select Relation
                                </InputLabel>
                                <Select
                                  size="small"
                                  id="family"
                                  name={`familyDetails.${index}.RelationshipWithStudent`}
                                  value={familyMember.RelationshipWithStudent}
                                  onChange={handleChange}
                                  // onBlur={handleBlur}
                                  fullWidth
                                  IconComponent={ExpandMoreIcon}
                                  // displayEmpty
                                  label="Select Relation"
                                >
                                  <MenuItem sx={{color:"#8E9091"}} disabled value="">
                                    Select
                                  </MenuItem>
                                  {[
                                    { name: "Father", value: "father" },
                                    { name: "Mother", value: "mother" },
                                    { name: "Spouse", value: "spouse" },
                                    { name: "Guardian", value: "guardian" },
                                  ].map((option) => (
                                    <MenuItem sx={{color:"#495052"}} key={option.value} value={option.value}>
                                      {option.name}
                                    </MenuItem>
                                  ))}
                                </Select>
                              </FormControl>
                              <ErrorMessage
                                name={`familyDetails.${index}.RelationshipWithStudent`}
                                component="div"
                                className="error"
                              />
                            </Grid>
                            <Grid xs={12} sm={6}></Grid>

                            <Grid item xs={12} sm={6}>
                              <Field
                                as={TextField}
                                size="small"
                                name={`familyDetails.${index}.GuardianFirstName`}
                                placeholder="First Name"
                                value={familyMember.GuardianFirstName}
                                onChange={handleChange}
                                onBlur={handleBlur}
                                fullWidth
                                label="First Name"
                                onkeydown={(e:any) => {
                                  if (familyMember.GuardianFirstName && familyMember.GuardianFirstName?.length >= 50 && e.key !== 'Backspace' && e.key !== 'Delete') {
                                    e.preventDefault();
                                  }
                                }}
                                inputProps={{
                                  maxLength: 50,
                                }}
                              />
                              <ErrorMessage
                                name={`familyDetails.${index}.GuardianFirstName`}
                                component="div"
                                className="error"
                              />
                            </Grid>

                            <Grid item xs={12} sm={6}>
                              <Field
                                as={TextField}
                                size="small"
                                name={`familyDetails.${index}.GuardianLastName`}
                                placeholder="Last Name"
                                value={familyMember.GuardianLastName}
                                onChange={handleChange}
                                onBlur={handleBlur}
                                fullWidth
                                label="Last Name"
                                inputProps={{
                                  maxLength: 50,
                                }}
                                onkeydown={(e:any) => {
                                  if (familyMember.GuardianLastName && familyMember.GuardianLastName?.length >= 50 && e.key !== 'Backspace' && e.key !== 'Delete') {
                                    e.preventDefault();
                                  }
                                }}

                              />
                              <ErrorMessage
                                name={`familyDetails.${index}.GuardianLastName`}
                                component="div"
                                className="error"
                              />
                            </Grid>

                            <Grid item xs={12} sm={6}>
                              <Field
                                as={TextField}
                                size="small"
                                type="email"
                                name={`familyDetails.${index}.GuardianEmail`}
                                placeholder="Email"
                                value={familyMember.GuardianEmail}
                                onChange={handleChange}
                                onBlur={handleBlur}
                                fullWidth
                                label="Email"

                              />
                              <ErrorMessage
                                name={`familyDetails.${index}.GuardianEmail`}
                                component="div"
                                className="error"
                              />
                            </Grid>

                            <Grid item xs={12} sm={6}>
                              <ReactPhoneInput
                                value={`${familyMember.GuardianContactNumber.countryCode}${familyMember.GuardianContactNumber.number}`}
                                onChange={(value, data: any) => {
                                  const dialCode = data?.dialCode || "";
                                  setFieldValue(`familyDetails.${index}.GuardianContactNumber.countryCode`, "+" + dialCode);
                                  setFieldValue(`familyDetails.${index}.GuardianContactNumber.number`, value.slice(dialCode.length).trim());
                                }}
                                country="in"
                              />
                              <ErrorMessage
                                name={`familyDetails.${index}.GuardianContactNumber.number`}
                                component="div"
                                className="error"
                              />
                            </Grid>

                            <Grid item xs={12} sm={6}>
                              <Field
                                as={TextField}
                                size="small"
                                name={`familyDetails.${index}.GuardianOccupation`}
                                placeholder="Occupation"
                                value={familyMember.GuardianOccupation}
                                onChange={handleChange}
                                onBlur={handleBlur}
                                fullWidth
                                label="Occupation"

                              />
                              <ErrorMessage
                                name={`familyDetails.${index}.GuardianOccupation`}
                                component="div"
                                className="error"
                              />
                            </Grid>

                            <Grid item xs={12} sm={6}>
                              <Field
                                as={TextField}
                                size="small"
                                name={`familyDetails.${index}.GuardianQualification`}
                                placeholder="Educational Qualification"
                                value={familyMember.GuardianQualification}
                                onChange={handleChange}
                                onBlur={handleBlur}
                                fullWidth
                                label="Qualification"
                              />
                            </Grid>

                            {/* Delete Button */}
                            <Grid item xs={12}>
                              <Box sx={{ display: "flex", justifyContent: "end" }}>
                                <img
                                  src={Images.deleteIcon}
                                  alt="delete"
                                  style={{ width: "1.5rem" }}
                                  onClick={() => {
                                    setFieldValue('familyDetails', getNewFamilyArrayAfterDelete(values.familyDetails, index));
                                  }}
                                />
                              </Box>
                            </Grid>
                          </Grid>
                        </div>
                      ))}
                      <Stack
                        direction="row"
                        sx={{ mt: 5, mb: 1, display: "flex", justifyContent: "end", mr: 10 }}
                        spacing={2}
                      >
                        <Button
                          onClick={() =>
                            push({
                              GuardianFirstName: "",
                              GuardianLastName: "",
                              GuardianEmail: "",
                              GuardianOccupation: "",
                              GuardianQualification: "",
                              RelationshipWithStudent: "",
                              GuardianContactNumber: { number: "", countryCode: "" },
                            })
                          }
                          sx={[secondaryButton, { width: "100px" ,color:"#495052" }]}
                        >
                          Add new
                        </Button>
                        <CustomButton
                          width="35%"
                          handleSubmit={handleSubmit}
                        >
                            {loading ? (
                              <img
                                src={Images.standardLoader}
                                alt="load"
                                style={{ width: "1rem",height:"1rem",padding:"4px",color:"#495052" }}
                              />
                            ) : (
                              "Save"
                            )}
                        </CustomButton>
                      </Stack>
                    </div>
                  )}
                </FieldArray>
              </form>
            )}
          </Formik>
        </Box>
      ) : (
        <Box sx={{ boxShadow: 2, borderRadius: 3, background: '#fff', p: 3}}>
          <Grid container >
            <Grid item xs={12} sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
              <span style={{ fontWeight: 600, fontSize: '1.25rem',color:'#8E9091' }}>Family Details</span>
              <Button variant="outlined" sx={[{ border:'2px solid #F48438 ', borderRadius:'20px',color:'#495052' ,fontSize: { lg: "16px", xs: "12px" }, width: { lg: "100px", xs: "90px" }, padding: { lg: "4px", xs: "6px" } }]} onClick={() => setFormEdit(true)}>
                Edit
              </Button>
            </Grid>
            {familyData.data && familyData.data.length > 0 ? (
              familyData.data.map((member: any, idx: number) => (
                <Grid item xs={12} key={idx} >
                  <Box sx={{ p: 2}}>
                    <Grid container spacing={2}>
                      <Grid item xs={12} sm={6}>
                        <div style={{ marginBottom: 10 }}>
                          <span style={{ color: "#8E9091", fontWeight: 500, marginRight: 8 }}>First Name</span>
                          <span style={{color:"#495052"}}>{member.GuardianFirstName || 'NA'}</span>
                        </div>
                        <div style={{ marginBottom: 10}}>
                          <span style={{ color: "#8E9091", fontWeight: 500, marginRight: 8 }}>Last Name</span>
                          <span style={{color:"#495052"}}>{member.GuardianLastName || 'NA'}</span>
                        </div>
                        <div style={{ marginBottom:10 }}>
                          <span style={{ color: "#8E9091", fontWeight: 500, marginRight: 8 }}>Email</span>
                          <span style={{color:"#495052"}}>{member.GuardianEmail || 'NA'}</span>
                        </div>
                        <div style={{ marginBottom: 10 }}>
                          <span style={{ color: "#8E9091", fontWeight: 500, marginRight: 8 }}>Relationship</span>
                          <span style={{color:"#495052"}}>{member.RelationshipWithStudent || 'NA'}</span>
                        </div>
                      </Grid>
                      <Grid item xs={12} sm={6}>
                        <div style={{ marginBottom: 10 }}>
                          <span style={{ color: "#8E9091", fontWeight: 500, marginRight: 8 }}>Occupation</span>
                          <span style={{color:"#495052"}}>{member.GuardianOccupation || 'NA'}</span>
                        </div>
                        <div style={{ marginBottom: 10 }}>
                          <span style={{ color: "#8E9091", fontWeight: 500, marginRight: 8 }}>Qualification</span>
                          <span style={{color:"#495052"}}>{member.GuardianQualification || 'NA'}</span>
                        </div>
                        <div style={{ marginBottom: 10}}>
                          <span style={{ color: "#8E9091", fontWeight: 500, marginRight: 8 }}>Contact Number</span>
                          <span style={{color:"#495052"}}>{member.GuardianContactNumber?.countryCode && member.GuardianContactNumber?.number ? `${member.GuardianContactNumber.countryCode} ${member.GuardianContactNumber.number}` : 'NA'}</span>
                        </div>
                      </Grid>
                    </Grid>
                  </Box>
                </Grid>
              ))
            ) : (
              <Grid sx={{color:"#495052"}} item xs={12}><div>No family details available.</div></Grid>
            )}
          </Grid>
        </Box>
      )}
    </>
  );
};

export default Family;
