const ScholarshipCarousel = () => {
  const [index, setIndex] = useState(0);

  useEffect(() => {
    const timer = setTimeout(() => {
      setIndex((prev) => (prev + 1) % carouselData.length);
    }, 2000);
    return () => clearTimeout(timer);
  }, [index]);

  const card = carouselData[index];

  return (
    <>
      <Card
        sx={{
          background: card.bg,
          borderRadius: { xs: '16px', sm: '24px', md: '32px' },
          mt: { xs: 2, sm: 3, md: 4 },
          p: { xs: 2, sm: 3, md: 4 },
          color: '#fff',
          display: 'flex',
          alignItems: 'flex-start',
          minHeight: { xs: 140, sm: 150, md: 160 },
          boxShadow: 0,
          position: 'relative',
          overflow: 'visible',
        }}
      >
        <Box sx={{ flex: 1, pr: { xs: 1, sm: 1.5, md: 2 } }}>
          <Typography 
            fontWeight={500} 
            fontSize={{ xs: "0.95rem", sm: "1rem", md: "1.1rem" }} 
            mb={1} 
            sx={{ color: '#fff', textAlign: 'left' }}
          >
            {card.title}
          </Typography>
          <Typography 
            fontWeight={300} 
            fontSize={{ xs: "0.85rem", sm: "0.9rem", md: "1.0rem" }} 
            mb={1} 
            sx={{ color: '#fff', textAlign: 'left' }}
          >
            {card.subtitle}
          </Typography>
          <Typography 
            fontSize={{ xs: "0.7rem", sm: "0.75rem", md: "0.8rem" }} 
            mb={2} 
            sx={{ color: '#fff', textAlign: 'left' }}
          >
            {card.description}
          </Typography>
          <Button
            variant="contained"
            sx={{
              background: '#fff',
              color: '#222',
              borderRadius: '24px',
              fontWeight: 700,
              px: { xs: 2, sm: 2.5, md: 3 },
              py: { xs: 0.75, sm: 0.875, md: 1 },
              textTransform: 'none',
              boxShadow: '0 2px 8px 0 rgba(0,0,0,0.10)',
              minWidth: { xs: 100, sm: 110, md: 120 },
              fontSize: { xs: "0.75rem", sm: "0.8rem", md: "0.875rem" },
              '&:hover': { background: '#f5f5f5', color: '#222' },
            }}
          >
            {card.button}
          </Button>
        </Box>
        <Box sx={{ 
          position: 'absolute', 
          right: { xs: 16, sm: 24, md: 32 }, 
          bottom: 0,
          display: { xs: 'none', sm: 'block' },
          '& img': {
            width: '100%',
            maxWidth: { xs: '100px', sm: '110px', md: '120px' },
            height: 'auto',
            borderRadius: 0
          }
        }}>
          <img
            src={card.image}
            alt={card.title}
          />
        </Box>
      </Card>
      {/* Carousel dots */}
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', mt: 1, mb: 2 }}>
        {carouselData.map((_, i) => (
          <Box
            key={i}
            sx={{
              width: 10,
              height: 10,
              borderRadius: '50%',
              background: i === index ? '#A993FF' : '#F2EFFF',
              mx: 0.5
            }}
          />
        ))}
      </Box>
    </>
  );
};

