import { Box, Typography } from "@mui/material";
import React, { useState } from "react";
import { useAppSelector } from "../../../../assets/hooks";
import Accordion from "../../../../genericComponents/accordian";
import Bachelors from "./bachelors";
import Diploma from "./diploma";
import School from "./grade10";
import PostGrad from "./postGrad";
import { useProfileData } from "src/genericComponents/modalPopup/useProfileData";

const Education = () => {
  const education = useAppSelector((state) => state.educationhistory);
  const [openIndex, setOpenIndex] = useState<number | null>(null);
  const { profileData } = useProfileData();

  const handleToggle = (index: number) => {
    setOpenIndex((prevIndex) => (prevIndex === index ? null : index));
  };

  const profile = [
    {
      title: "Masters",
      component: <PostGrad data={education.data.postGraduation} profileData={profileData} />,
    },
    {
      title: "Under Graduation",
      component: <Bachelors data={education.data.underGraduation} profileData={profileData} />,
    },
    {
      title: "Diploma / Grade 12",
      component: <Diploma data={education.data.plus2} profileData={profileData} />,
    },
    {
      title: "Grade 10",
      component: <School data={education.data.school} profileData={profileData} />,
    },
  ];

  return (
    <Box
      sx={{
        background: "#fff",
        borderRadius: "10px",
        height: "75vh",
        overflowY: "scroll",
        px: 3,
      }}
    >
      <Typography fontSize="1rem" gutterBottom color="#495052" sx={{ m: 1, fontWeight: "500" }}>
        Enter your current and last education details
      </Typography>
      {profile.map((item, index) => (
        <Accordion
          key={index}
          title={item.title}
          isOpen={openIndex === index} //  Pass open state
          onToggle={() => handleToggle(index)} //  Toggle accordion
        >
          {item.component}
        </Accordion>
      ))}
    </Box>
  );
};

export default Education;
