/* eslint-disable */
import { Box, Button, Grid, Stack, Tooltip, Typography } from "@mui/material";
import CustomCard from "../../../../genericComponents/customCard";
// import WatchLaterIcon from "@mui/icons-material/WatchLater";
import { useAppDispatch, useAppSelector } from "../../../../assets/hooks";
import { Application } from "../../../../types/types";
import Images from "../../../../assets";
import TimerIcon from "@mui/icons-material/Timer";
import { setPopup } from "../../../../store/Slices/popupSlice";
import { useNavigate } from "react-router-dom";

const AppliedUniversities = () => {
  const navigate =useNavigate()
  const applicationsData = useAppSelector((state) => state.applications);
  const dispatch = useAppDispatch();
  function date(date: Date) {
    const month = date.toLocaleString("default", { month: "short" });
    const year = date.getFullYear();
    return month + " " + year;
  }
  return (
    <div
      style={{
        // maxHeight: "81vh",
        overflowY: "scroll",
        paddingTop:"1rem"
      }}
    >
      <Grid container spacing={3}>
        {applicationsData?.data &&
        applicationsData?.data?.length > 0 ? (
          <>
                     {applicationsData?.data?.map(
              (item: Application, i: number) => (
                <Grid item xs={12} sm={6} lg={4} sx={{paddingLeft:{xs:0,md:"1rem"}}} key={i}>
                  <CustomCard 
                    title={item.stage} 
                    i={i} 
                    titleIcon={<TimerIcon sx={{ fontSize: "1rem", marginRight: "4px", color: "#495052" }} />}
                    badgePosition="top-left"
                    badgeStyle={{
                      backgroundColor: "#fff",
                      border: "1px solid #D0D0D0",
                      borderRadius: "20px",
                      top: "-8px",
                      left: "18px",
                      paddingLeft: "12px",
                      paddingRight: "12px",
                      paddingTop: "6px",
                      paddingBottom: "6px",
                    }}
                  >
                    <Box
                      onClick={() =>
                        dispatch(
                          setPopup({
                            show: true,
                            data: {
                              container: {
                                name: "applicationPopup",
                                data: item._id,
                                dimensions: {
                                  width: "800px",
                                  height:"85%"
                                },
                              },
                              type: "custom",
                            },
                          })
                        )
                      }
                      sx={{
                        position: "relative",
                        height: "100%",
                        display: "flex",
                        flexDirection: "column",
                        pt: 5, // Space for the badge
                      }}
                    >
                        {/* Rejection Request Ribbon */}
                        {item?.cancellationRequest === true && (
                          <Box
                            sx={{
                              position: "absolute",
                              top: 0,
                              right: 0,
                              width: "140px",
                              height: "140px",
                              overflow: "hidden",
                              zIndex: 3,
                            }}
                          >
                            <Box
                              sx={{
                                position: "absolute",
                                top: "25px",
                                right: "-35px",
                                width: "180px",
                                height: "32px",
                                backgroundColor: "#FF4444",
                                transform: "rotate(45deg)",
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                                boxShadow: "0 2px 6px rgba(0,0,0,0.25)",
                              }}
                            >
                              <Typography
                                sx={{
                                  color: "#fff",
                                  fontSize: "0.7rem",
                                  fontWeight: 600,
                                  transform: "rotate(-45deg)",
                                  textAlign: "center",
                                  px: 1,
                                  letterSpacing: "0.3px",
                                }}
                              >
                                Rejection request
                              </Typography>
                            </Box>
                          </Box>
                        )}
                        
                        {/* University Logo Container */}
                        <Box 
                          sx={{ 
                            display: "flex", 
                            alignItems: "center", 
                            mb: 2,
                            mt: 1,
                          }}
                        >
                          <Box
                            sx={{
                              width: "48px",
                              height: "48px",
                              borderRadius: "8px",
                              border: "1px solid #D0D0D0",
                              backgroundColor: "#fff",
                              display: "flex",
                              alignItems: "center",
                              justifyContent: "center",
                              padding: "4px",
                              flexShrink: 0,
                            }}
                          >
                            <img
                              src={item.course?.university?.logoSrc}
                              alt="logo"
                              style={{
                                width: "100%",
                                height: "100%",
                                objectFit: "contain",
                              }}
                            />
                          </Box>
                        </Box>

                        {/* Course Title */}
                        <Tooltip
                          slotProps={{
                            tooltip: {
                              sx: {
                                color: "#fff",
                                backgroundColor: "#3B3F76",
                                boxShadow: 1,
                              },
                            },
                          }}
                          title={item.course.name}
                          placement="bottom"
                        >
                          <Typography
                            sx={{
                              fontSize: { xs: "1rem", lg: "1.1rem" },
                              fontWeight: 600,
                              color: "#2C2F5C",
                              mb: 1,
                              textOverflow: "ellipsis",
                              overflow: "hidden",
                              display: "-webkit-box",
                              WebkitLineClamp: 2,
                              WebkitBoxOrient: "vertical",
                              lineHeight: 1.3,
                              minHeight: "2.6rem",
                            }}
                          >
                            {item.course.name}
                          </Typography>
                        </Tooltip>

                        {/* University and Country */}
                        <Typography
                          sx={{
                            fontSize: { xs: "0.75rem", lg: "0.8rem" },
                            color: "#8E9091",
                            mb: 3,
                            textOverflow: "ellipsis",
                            overflow: "hidden",
                            display: "-webkit-box",
                            WebkitLineClamp: 1,
                            WebkitBoxOrient: "vertical",
                          }}
                        >
                          {item.course?.university?.name} | {item.course?.university?.location?.country}
                        </Typography>

                        {/* Start Date at Bottom */}
                        <Box sx={{ mt: "auto", pt: 2 }}>
                          <Typography
                            sx={{
                              fontSize: { xs: "0.75rem", lg: "0.8rem" },
                              fontWeight: 500,
                              color: "#495052",
                              display: "flex",
                              alignItems: "center",
                              gap: 0.5,
                            }}
                          >
                            <TimerIcon sx={{ fontSize: "1rem" }} />
                            Start date : {item?.intake ? date(new Date(item?.intake)) : "N/A"}
                          </Typography>
                        </Box>
                      </Box>
                  </CustomCard>
                </Grid>
              )
            )}
          </>
        )  : (
          <Grid
            item
            xs={12}
            sx={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "center",
              height: "60vh",
              minHeight: "350px",
            }}
          >
            <img
              src={Images.Dull}
              alt="noData"
              style={{ width: "230px", height: "220px", marginBottom: "1.5rem" }}
            />
            <Typography
              sx={{
                mb: 1,
                fontSize: "1.6rem",
                fontWeight: 600,
                color: "#495052",
                textAlign: "center",
              }}
            >
             No Products Added Yet
            </Typography>
            <Typography
              sx={{
                mb: 3,
                fontSize: "1rem",
                color: "#8E9091",
                textAlign: "center",
              }}
            >
             You haven't saved any courses yet-start exploring and <br/> discover your next opportunity!
            </Typography>
            <Button
              sx={{
                background: "#F48438",
                color: "#fff",
                borderRadius: "20px",
                px: 4,
                py: 1,
                fontWeight: 500,
                fontSize: "1rem",
                textTransform: "none",
                boxShadow: "none",
                "&:hover": {
                  background: "#F48438",
                },
              }}
              onClick={() => {
                const subDisUrl = `activeTab=${encodeURIComponent(JSON.stringify('courses'))}&FilterData=${encodeURIComponent(JSON.stringify([]))}&page=${JSON.stringify(
                  1
                )}&search=${encodeURIComponent(JSON.stringify(''))}`
                const subDisciUrl = window.btoa(subDisUrl);
                navigate(`/explore?${subDisciUrl}`);
              }}
            >
              Add products
            </Button>
          </Grid>
        )}
      </Grid>
    </div>
  );
};

export default AppliedUniversities;
