import React, { useState, useEffect } from "react";
import {
  Button,
  Grid,
  TextField,
  Box,
  InputLabel,
  Stack,
  MenuItem,
  Select,
  FormControlLabel,
  Radio,
  // FormControl,
  RadioGroup,
  Typography,
  FormControl,
} from "@mui/material";
import Images from "../../../../assets";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { CustomButton } from "../../../../genericComponents/customButton";
import { editProfile, getProfile } from "../../../../services";
import { useAppDispatch, useAppSelector } from "../../../../assets/hooks";
import { setExtraCurriculum } from "../../../../store/Slices/extraCurricular";
import { setPopup } from "../../../../store/Slices/popupSlice";
import { Formik, Field, FieldArray, ErrorMessage, Form } from "formik";
import * as Yup from "yup";
import { secondaryButton } from "../../../../components/styles/customStyles";
import Skills from "./preferences";
import DatePickerComponent from "src/genericComponents/datePicker";
// import { useState } from "react";
// import { Grid, RadioGroup, FormControlLabel, Radio, Typography } from "@mui/material";


interface ExtraCurricularMember {
  activity: string;
  description: string;
  startDate: any;
  endDate: any;
  designation: string;
  status: string;
  participatedPublication?: string;
  participationLevel?: string;
}

interface ResearchPaper {
  title: string;
  journal: string;
  year: string;
  authors: string;
  description: string;
}

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 150,
    },
  },
};

const validationSchema = Yup.object().shape({
  activity: Yup.string().required("Activity is required"),
  description: Yup.string().required("Description is required"),
  startDate: Yup.date().required("Start Date is required"),
  endDate: Yup.date()
    .nullable() 
    .when("status", {
      is: "completed", 
      then: (schema) =>
        schema.required("End Date is required").min(
          Yup.ref("startDate"),
          "End Date cannot be earlier than Start Date"
        ),
      otherwise: (schema) => schema.nullable(), 
    }),
  status: Yup.string().required("Status is required"),
});

const validationSchemaArray = Yup.array().of(validationSchema);

const ExtraCurricular = () => {
  const dispatch = useAppDispatch();
  const [loading, setLoading] = useState(false);
  const extracurricular = useAppSelector((state) => state.extracurriculum);
  const [formEdit, setFormEdit] = useState(false);
  const initialValues = {
    extraCurriculumActivities: (extracurricular.data?.length
      ? extracurricular.data
      : [
        {
          activity: "",
          description: "",
          designation: "",
          startDate: "",
          endDate: "",
          status: "",
        },
      ]
    ).map((item: any) => ({
      ...item,
      participatedPublication: item.participatedPublication ?? "No",
      participationLevel: item.participationLevel ?? "",
    })),
  };

  const activities = [
    "Dance",
    "Online Certification",
    "Paper Publication",
    "Project",
    "Research",
    "Student Government",
    "Academic Teams and Clubs",
    "The Debate Team Students",
    "Internships",
  ];

  const [researchPapers] = useState<ResearchPaper[]>([]);
  const skills = useAppSelector((state) => state.skills.data);

  useEffect(() => {
    async function fetchExtraCurricular() {
      try {
        const profile = await getProfile();
        console.log('Fetched profile for extraCurricular:', profile);
        if (profile && profile.data && profile.data.extraCurriculumActivities) {
          dispatch(setExtraCurriculum(profile.data.extraCurriculumActivities));
        } else if (profile && profile.extraCurriculumActivities) {
          dispatch(setExtraCurriculum(profile.extraCurriculumActivities));
        }
      } catch (error) {
        // Optionally handle error
      }
    }
    fetchExtraCurricular();
  }, [dispatch]);

  const submitForm = async (values: any) => {
    if (!loading) {
      try {
        setLoading(true);
        // Ensure all activities have the new fields
        const activitiesWithPublication = values.extraCurriculumActivities.map((item: any) => ({
          ...item,
          participatedPublication: item.participatedPublication ?? "No",
          participationLevel: item.participationLevel ?? "",
        }));
        console.log('Submitting activities:', activitiesWithPublication);
        const response = await editProfile({
          extraCurriculumActivities: activitiesWithPublication,
          researchPapers: researchPapers,
        });
        console.log('Submit response:', response);
        if (response && response.data && response.data.extraCurriculumActivities) {
          dispatch(setExtraCurriculum(response.data.extraCurriculumActivities));
        } else if (response && response.data && response.data.data && response.data.data.extraCurriculumActivities) {
          dispatch(setExtraCurriculum(response.data.data.extraCurriculumActivities));
        }
        if (response) {
          dispatch(
            setPopup({
              show: true,
              data: {
                container: {
                  name: "success",
                  data: "Extra curricular activities and research papers added successfully",
                  dimensions: {
                    width: "40%",
                    height: "40%",
                    show: "none"
                  },
                },
                type: "custom",
              },
            })
          );
        }
        setLoading(false);
      } catch (error) {
        setLoading(false);
      }
    }
  };


  return (
    <>
      {formEdit ? (
        <Box sx={{ overflowY: "scroll", px: 3, height: "75vh" }}>
          <Box sx={{ px: 2 }}>
            <Skills />
          </Box>
          <Box >
            <Typography sx={{ fontSize: "1.25rem", fontWeight: "500", mb: 1, color: "rgba(48, 51, 99, 0.5)", px: 2 }}>Activities</Typography>
            <Formik
              initialValues={initialValues}
              validationSchema={Yup.object().shape({
                extraCurriculumActivities: validationSchemaArray,
              })}
              onSubmit={(values, { setSubmitting }) => {
                submitForm(values);
                setSubmitting(false);
              }}
            >
              {({ values, handleChange, handleBlur, isValid, dirty }) => (
                <Form >
                  <FieldArray name="extraCurriculumActivities">
                    {({ push, remove }) => (
                      <>
                        {values.extraCurriculumActivities.map((activity: ExtraCurricularMember, index) => (
                          <div className="mb-5" key={index}>
                            <Grid container spacing={4} sx={{ px: 2 }}>
                              {/* Activity Field */}
                              <Grid item xs={12} sm={6}>
                                <InputLabel id="" sx={{ color: "#8E9091", fontWeight: 500 }}>
                                  Activity
                                </InputLabel>
                                <FormControl fullWidth variant="outlined" size="small" sx={{ mt: 1,color:"#8E9091" }}>
                                  <Field
                                    as={Select}
                                    size="small"
                                    name={`extraCurriculumActivities.${index}.activity`}
                                    value={activity.activity}
                                    onBlur={handleBlur}
                                    onChange={handleChange}
                                    fullWidth
                                    displayEmpty
                                    IconComponent={ExpandMoreIcon}
                                    MenuProps={MenuProps}
                                  >
                                    <MenuItem sx={{color:"#8E9091"}} disabled value="">
                                      Select activity
                                    </MenuItem >
                                    {activities.map((value) => (
                                      <MenuItem sx={{color:"#495052"}} key={value} value={value}>
                                        {value}
                                      </MenuItem>
                                    ))}
                                  </Field>
                                  <ErrorMessage
                                    name={`extraCurriculumActivities.${index}.activity`}
                                    component="div"
                                    className="error"
                                  />
                                </FormControl>
                              </Grid>

                              {/* Designation Field */}
                              <Grid item xs={12} sm={6}>
                                <InputLabel id="" sx={{ color: "#8E9091", fontWeight: 500,mb: 1}}>
                                  Designation
                                </InputLabel>
                                <Field
                                  as={TextField}
                                  size="small"
                                  name={`extraCurriculumActivities.${index}.designation`}
                                  placeholder="Designation"
                                  value={activity.designation}
                                  onBlur={handleBlur}
                                  onChange={handleChange}
                                  fullWidth
                                />
                                <ErrorMessage
                                  name={`extraCurriculumActivities.${index}.designation`}
                                  component="div"
                                  className="error"
                                />
                              </Grid>

                              {/* Status Field */}
                              <Grid item xs={12} sm={6}>
                                <InputLabel id="" sx={{ color: "#8E9091", fontWeight: 500 }}>
                                  Active status
                                </InputLabel>
                                <Field
                                  as={RadioGroup}
                                  row
                                  name={`extraCurriculumActivities.${index}.status`}
                                  value={activity.status}
                                  onBlur={handleBlur}
                                  onChange={handleChange}
                                >
                                  <FormControlLabel
                                    value="completed"
                                    control={<Radio size="small" />}
                                    label="Completed"
                                  />
                                  <FormControlLabel
                                    value="ongoing"
                                    control={<Radio size="small" />}
                                    label="Ongoing"
                                  />
                                </Field>
                                <ErrorMessage
                                  name={`extraCurriculumActivities.${index}.status`}
                                  component="div"
                                  className="error"
                                />
                              </Grid>

                              {/* Description Field */}
                              <Grid item xs={12} sm={6}>
                                <InputLabel id="" sx={{ color: "#8E9091", fontWeight: 500,mb: 1 }}>
                                  Description
                                </InputLabel>
                                <Field
                                  as={TextField}
                                  size="small"
                                  name={`extraCurriculumActivities.${index}.description`}
                                  placeholder="Description"
                                  value={activity.description}
                                  onBlur={handleBlur}
                                  onChange={handleChange}
                                  fullWidth
                                />
                                <ErrorMessage
                                  name={`extraCurriculumActivities.${index}.description`}
                                  component="div"
                                  className="error"
                                />
                              </Grid>

                              {/* Start Date Field */}
                              <Grid item xs={12} sm={6}>
                                <InputLabel id="" sx={{ color: "#8E9091", fontWeight: 500,mb: 1 }}>
                                  Attended from
                                </InputLabel>

                              
                                <DatePickerComponent
                                  selectedDate={activity?.startDate}
                                  onChange={(date) => handleChange({ target: { name: `extraCurriculumActivities.${index}.startDate`, value: date?.toISOString().slice(0, 10) } })}
                                  maxDate={new Date()}
                                  placeholderText="Start Date"
                                />
                                <ErrorMessage
                                  name={`extraCurriculumActivities.${index}.startDate`}
                                  component="div"
                                  className="error"
                                />
                              </Grid>

                                <Grid item xs={12} sm={6}>
        <Typography sx={{ fontWeight: 500, color: "#8E9091", mb: 1 }}>
          Did you participate any publication?
        </Typography>
        <Field
          as={RadioGroup}
          row
          name={`extraCurriculumActivities.${index}.participatedPublication`}
          value={activity.participatedPublication || "No"}
          onChange={handleChange}
        >
          <FormControlLabel value="Yes" control={<Radio size="small" />} label="Yes" />
          <FormControlLabel value="No" control={<Radio size="small" />} label="No" />
        </Field>
      </Grid>

     


                              {/* End Date Field */}
                              <Grid item xs={12} sm={6}>
                                {activity.status === "completed" && (
                                  <>
                                    <InputLabel id="" sx={{ color: "#8E9091", fontWeight: 500 }}>
                                      Attended till
                                    </InputLabel>
                                    <DatePickerComponent
                                      selectedDate={activity?.endDate}
                                      onChange={(date) => handleChange({ target: { name: `extraCurriculumActivities.${index}.endDate`, value: date?.toISOString().slice(0, 10) } })}
                                      minDate={
                                        activity.startDate
                                          ? new Date(
                                            new Date(activity?.startDate).getTime() + 10 * 24 * 60 * 60 * 1000
                                          )
                                          : undefined
                                      }
                                      maxDate={new Date()}
                                      placeholderText="End Date"
                                    />
                                    <ErrorMessage
                                      name={`extraCurriculumActivities.${index}.endDate`}
                                      component="div"
                                      className="error"
                                    />
                                  </>
                                )}
                              </Grid>

                              {activity.participatedPublication === "Yes" && (
                                <Grid item xs={12} sm={6}>
                                  <Typography sx={{ fontWeight: 500, color: "#8E9091", mb: 1 }}>
                                    Participation Level
                                  </Typography>
                                  <Field
                                    as={RadioGroup}
                                    row
                                    name={`extraCurriculumActivities.${index}.participationLevel`}
                                    value={activity.participationLevel || ""}
                                    onChange={handleChange}
                                  >
                                    <FormControlLabel value="National" control={<Radio size="small" />} label="National" />
                                    <FormControlLabel value="International" control={<Radio size="small" />} label="International" />
                                  </Field>
                                </Grid>
                              )}

                              {/* Remove Activity Button */}
                              {values.extraCurriculumActivities.length > 1 && (
                                <Grid item xs={12}>
                                  <Box sx={{ display: "flex", justifyContent: "end" }}>
                                    <img
                                      src={Images.deleteIcon}
                                      alt="delete"
                                      style={{ width: "1.5rem" }}
                                      onClick={() => remove(index)}
                                    />
                                  </Box>
                                </Grid>
                              )}
                            </Grid>
                          </div>
                        ))}

                        <Stack
                          direction="row"
                          sx={{ mt: 5, mb: 1, display: "flex", justifyContent: "end", mr: 10 }}
                          spacing={2}
                        >
                          <Button
                            onClick={() =>
                              push({
                                activity: "",
                                description: "",
                                designation: "",
                                startDate: "",
                                endDate: "",
                                status: "",
                                participatedPublication: "No",
                                participationLevel: "",
                              })
                            }
                            sx={secondaryButton}
                          >
                            Add new
                          </Button>
                          <CustomButton width="100px" handleSubmit={()=>submitForm}>
                            {loading ? (
                              <img
                                src={Images.standardLoader}
                                alt="load"
                                style={{ width: "1rem",height:"1rem",padding:"4px" }}
                              />
                            ) : (
                              "Save"
                            )}
                          </CustomButton>
                        </Stack>
                      </>
                    )}
                  </FieldArray>
                </Form>
              )}
            </Formik>
          </Box>
          {/* <Box sx={{ mt: 4, px: 2 }}>
            <Typography sx={{ fontSize: "1.25rem", fontWeight: "500", mb: 1, color: "rgba(48, 51, 99, 0.5)" }}>
              Research Papers
            </Typography>
            {researchPapers.map((paper, idx) => (
              <Box key={idx} sx={{ mb: 2, border: '1px solid #eee', borderRadius: 2, p: 2 }}>
                <Grid container spacing={2}>
                  <Grid item xs={12} sm={6}>
                    <TextField
                      label="Title"
                      size="small"
                      fullWidth
                      value={paper.title}
                      onChange={e => {
                        const newPapers = [...researchPapers];
                        newPapers[idx].title = e.target.value;
                        setResearchPapers(newPapers);
                      }}
                    />
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <TextField
                      label="Journal"
                      size="small"
                      fullWidth
                      value={paper.journal}
                      onChange={e => {
                        const newPapers = [...researchPapers];
                        newPapers[idx].journal = e.target.value;
                        setResearchPapers(newPapers);
                      }}
                    />
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <TextField
                      label="Year"
                      size="small"
                      fullWidth
                      value={paper.year}
                      onChange={e => {
                        const newPapers = [...researchPapers];
                        newPapers[idx].year = e.target.value;
                        setResearchPapers(newPapers);
                      }}
                    />
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <TextField
                      label="Authors"
                      size="small"
                      fullWidth
                      value={paper.authors}
                      onChange={e => {
                        const newPapers = [...researchPapers];
                        newPapers[idx].authors = e.target.value;
                        setResearchPapers(newPapers);
                      }}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      label="Description"
                      size="small"
                      fullWidth
                      multiline
                      minRows={2}
                      value={paper.description}
                      onChange={e => {
                        const newPapers = [...researchPapers];
                        newPapers[idx].description = e.target.value;
                        setResearchPapers(newPapers);
                      }}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <Button
                      variant="outlined"
                      color="error"
                      onClick={() => {
                        setResearchPapers(researchPapers.filter((_, i) => i !== idx));
                      }}
                      disabled={researchPapers.length === 1}
                    >
                      Remove
                    </Button>
                  </Grid>
                </Grid>
              </Box>
            ))}
            <Button
              variant="outlined"
              onClick={() => setResearchPapers([...researchPapers, { title: "", journal: "", year: "", authors: "", description: "" }])}
            >
              Add Research Paper
            </Button>
          </Box> */}
        </Box>
      ) : (
        <Box sx={{ boxShadow: 2, borderRadius: 3, background: '#fff', pl: 3, mb: 3 }}>
          <Grid container spacing={2}>
            <Grid item xs={12} sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
              <span style={{ fontWeight: 600, fontSize: '1.25rem',color:"#8E9091" }}>Extra Curricular Details</span>
              <Button variant="outlined" sx={[{ border:'2px solid #F48438 ', borderRadius:'20px',color:'#495052' ,fontSize: { lg: "16px", xs: "12px" }, width: { lg: "100px", xs: "90px" }, padding: { lg: "8px", xs: "6px" } }]}onClick={() => setFormEdit(true)}>
                Edit
              </Button>
            </Grid>
            {extracurricular.data && extracurricular.data.length > 0 ? (
              extracurricular.data.map((item: any, idx: number) => (
                <Grid item xs={12} key={idx} sx={{ mb: 2 }}>
                  <Box sx={{ mb: 2 }}>
                    <Grid container spacing={2}>
                      <Grid item xs={12} sm={6}>
                        
                        <span style={{ color: "#8E9091", fontWeight: 500,display:'block' }}>Activity</span>
                        <div style={{color:"#495052"}}>{item.activity || 'NA'}</div>
                        <span style={{ color: "#8E9091", fontWeight: 500, marginTop: 16, display: 'block' }}>Skills</span>
                        <div style={{color:"#495052"}}>{skills && skills.length > 0 ? skills.join(', ') : 'NA'}
                        </div>
                        <span style={{ color: "#8E9091", fontWeight: 500, marginTop: 16, display: 'block' }}>Description</span>
                        <div style={{color:"#495052"}}>{item.description || 'NA'}</div>
                        <span style={{ color: "#8E9091", fontWeight: 500, marginTop: 16, display: 'block' }}>Publication Participation</span>
                        <div style={{color:"#495052"}}>{item.participatedPublication || 'NA'}</div>
                        {item.participatedPublication === 'Yes' && (
                          <>
                            <span style={{ color: "#8E9091", fontWeight: 500, marginTop: 16, display: 'block' }}>Participation Level</span>
                            <div style={{color:"#495052"}}>{item.participationLevel || 'NA'}</div>
                          </>
                        )}
                      </Grid>
                      <Grid item xs={12} sm={6}>
                        <span style={{ color: "#8E9091", fontWeight: 500, display: 'block' }}>Start Date</span>
                        <div style={{color:"#495052"}}>
                          {item.startDate && item.endDate
                            ? `${item.startDate.split('T')[0]}`
                            : item.startDate
                              ? item.startDate.split('T')[0]
                              : item.endDate
                                ? item.endDate.split('T')[0]
                                : ''}
                        </div>
                        <span style={{ color: "#8E9091", fontWeight: 500,marginTop:16,display:'block' }}>End Date</span>
                        <div style={{color:"#495052"}}>
                          {item.startDate && item.endDate
                            ? `${item.endDate.split('T')[0]}`
                            : item.startDate
                              ? item.startDate.split('T')[0]
                              : item.endDate
                                ? item.endDate.split('T')[0]
                                : ''}
                        </div>
                        <span style={{ color: "#8E9091", fontWeight: 500, marginTop: 16, display: 'block' }}>Designation</span>
                        <div style={{color:"#495052"}}>{item.designation || 'NA'}</div>
                        <span style={{ color: "#8E9091", fontWeight: 500, marginTop: 16, display: 'block' }}>Status</span>
                        <div style={{color:"#495052"}}>{item.status || 'NA'}</div>
                      </Grid>
                    </Grid>
                  </Box>
                </Grid>
              ))
            ) : (
              <Grid item xs={12}><div>No extra curricular details available.</div></Grid>
            )}
          </Grid>
        </Box>
      )}
      {/* Research Papers Section (Read-Only Mode) */}
      {!formEdit && researchPapers && researchPapers.length > 0 && (
        <Box sx={{ boxShadow: 2, borderRadius: 3, background: '#fff', p: 3, mb: 3, mt: 3 }}>
          <Typography variant="h6" sx={{ fontWeight: 600, mb: 2 }}>
            Research Papers
          </Typography>
          {researchPapers.map((paper: ResearchPaper, idx: number) => (
            <Box key={idx} sx={{ border: '1px solid #eee', borderRadius: 2, p: 2, mb: 2 }}>
              <div><strong>Title:</strong> {paper.title || 'NA'}</div>
              <div><strong>Journal:</strong> {paper.journal || 'NA'}</div>
              <div><strong>Year:</strong> {paper.year || 'NA'}</div>
              <div><strong>Authors:</strong> {paper.authors || 'NA'}</div>
              <div><strong>Description:</strong> {paper.description || 'NA'}</div>
            </Box>
          ))}
        </Box>
      )}
    </>
  );
};

export default ExtraCurricular;
