import React, { useRef, useState } from "react";
import { Button, Grid, Stack, Typography } from "@mui/material";
import FileUploadOutlinedIcon from "@mui/icons-material/FileUploadOutlined";
import Accordion from "../../../../genericComponents/accordian";
import { useAppDispatch } from "../../../../assets/hooks";
import { deleteUploadProfile, uploadProfile } from "../../../../services";
import { setDocuments } from "../../../../store/Slices/documentsSlice";
import { setPopup } from "../../../../store/Slices/popupSlice";
import VisibilityOutlinedIcon from '@mui/icons-material/VisibilityOutlined';
import DeleteOutlineOutlinedIcon from '@mui/icons-material/DeleteOutlineOutlined';
import ConfirmDialog from "src/genericComponents/modalPopup/confirmDialog";
import Images from "src/assets";

const AcademicDocs = ({ data }: any) => {
  const dispatch = useAppDispatch();

  const [openIndex, setOpenIndex] = useState<number | null>(null); // Manage open state centrally
  const [open, setOpen] = useState<boolean>(false);
  const [deleteItem, setDeleteItem] = useState<any | null>(null);
  const [identifier, setIdentifier] = useState<any | null>(null);

  const handleToggle = (index: number) => {
    setOpenIndex((prevIndex) => (prevIndex === index ? null : index));
  };

  const handleDeleteOpen = (name: any, item: any) => {
    setOpen(true);
    setDeleteItem(item);
    setIdentifier(name);
  };

  const inputFileRefs = {
    secondarySchool: useRef<HTMLInputElement>(null),
    plus2: useRef<HTMLInputElement>(null),
    degree: useRef<HTMLInputElement>(null),
  };

  const documents = [
    { key: "transcripts", label: "Transcripts" },
    { key: "bonafide", label: "Bonafide" },
    { key: "CMM", label: "CMM" },
    { key: "PCM", label: "PCM" },
    { key: "OD", label: "OD" },
  ];

  const secondaryDocuments = [
    { key: "secondarySchool", label: "School" },
    { key: "plus2", label: "Grade 12" },
    { key: "degree", label: "Degree" },
  ];
  const DocumentItem = ({ doc, data, handleDelete }: any) => {
    return (
      <Grid
        container
        sx={{
          display: "flex",
          alignItems: "center",
          borderRadius: "8px",
          padding: "10px",
          background: "#F7FAFD",
          mb: 2,
        }}
      >
        <Grid item xs={4}>
          <Typography fontWeight="500">{doc.label}</Typography>
        </Grid>
        <Grid item xs={8} sx={{ justifyContent: "end", display: "flex" }}>
          {data && data[doc.key] ? (
            <Stack
              direction="row"
              spacing={2}
              sx={{ alignItems: "center", marginRight: "10px" }}
            >
              <Typography>{data[doc.key]?.data?.FileName}</Typography>
              <VisibilityOutlinedIcon
                sx={{ fontSize: "large", cursor: "pointer" }}
                onClick={() => {
                  dispatch(
                    setPopup({
                      show: true,
                      data: {
                        container: {
                          name: "fileViewer",
                          data: data[doc.key]?.data?.preview_url,
                          dimensions: { width: "500px" },
                        },
                        type: "custom",
                      },
                    })
                  );
                }}
              />
              <DeleteOutlineOutlinedIcon
                sx={{ fontSize: "large", cursor: "pointer" }}
                onClick={() => handleDelete(`academic.${doc.key}`, data[doc.key]._id)}
              />
            </Stack>
          ) : (
            <Typography color="gray">No file uploaded</Typography>
          )}
        </Grid>
      </Grid>
    );
  };
  const handleDelete = async (fieldPath: string, deleteId: string) => {
    try {
      const response = await deleteUploadProfile({
        fieldPath: fieldPath,
        documentId: deleteId,
      });
  
      if (response) {
        dispatch(setDocuments(response.data.data)); // Update Redux store
        setOpen(false); // Close the dialog after deletion
      }
    } catch (error) {
      console.error("Failed to delete document:", error);
    }
  };
  

  const FileUploadSection = ({ title, docData, docPath, index }: any) => {
    return (
      <Accordion title={title} isOpen={openIndex === index} onToggle={() => handleToggle(index)}>
        <>
          {documents.map((doc) => (
            <DocumentItem
              key={doc.key}
              doc={doc}
              data={docData}
              handleDelete={handleDeleteOpen}
            />
          ))}
        </>
      </Accordion>
    );
  };

  return (
    <div>
      <Typography textAlign="right">
        <b>Note:</b> Only jpeg, png, pdf files are accepted
      </Typography>

      {/* Bachelors Section */}
      <FileUploadSection title="Bachelors" docData={data?.bachelors} docPath="academic.bachelors" index={0} />

      {/* Masters Section */}
      <FileUploadSection title="Masters" docData={data?.masters} docPath="academic.masters" index={1} />

      {/* Secondary Documents */}
      {secondaryDocuments.map((doc, idx) => (
        <DocumentItem
          key={doc.key}
          doc={doc}
          data={data}
          handleDelete={handleDeleteOpen}
        />
      ))}

<ConfirmDialog
  open={open}
  handleClose={() => setOpen(false)}
  additionalData={{
    onSubmit: () => handleDelete(identifier, deleteItem), //  Now handleDelete is properly defined
    title: "Are you sure you want to delete?",
    content: "",
  }}
/>

    </div>
  );
};

export default AcademicDocs;
