import { Box } from "@mui/material";
import React, { useState } from "react";
import Accordion from "../../../../genericComponents/accordian";
import PersonalDetails from "./personalDetails";
import Address from "./address";
import { useProfileData } from "src/genericComponents/modalPopup/useProfileData";

const Profile = () => {
  // State to track which accordion is open
  const [openIndex, setOpenIndex] = useState<number | null>(null);
  

  // Function to handle accordion toggling
  const handleToggle = (index: number) => {
    setOpenIndex((prevIndex) => (prevIndex === index ? null : index));
  };

  const profile = [
    {
      title: "My Details",
      component: <PersonalDetails />,
    },
    {
      title: "Address",
      component: <Address />,
    },
  ];

  return (
    <Box
      sx={{
        background: "#fff",
        borderRadius: "10px",
        height: "75vh",
        overflowY: "scroll",
        px: 3,
      }}
    >
      {profile.map((item, index) => (
        <Accordion
          key={index}
          title={item.title}
          isOpen={openIndex === index} // Pass state-controlled open status
          onToggle={() => handleToggle(index)} // Toggle function
        >
          {item.component}
        </Accordion>
      ))}
    </Box>
  );
};

export default Profile;
