import {
    Box,
    Button,
    FormControl,
    FormControlLabel,
    Grid,
    InputAdornment,
    InputLabel,
    ListSubheader,
    MenuItem,
    Radio,
    RadioGroup,
    Select,
    TextField,
    Typography,
  } from "@mui/material";
  import { ErrorMessage, Form, Formik } from "formik";
  import React, { useEffect, useMemo, useState } from "react";
  // import { DestinationTypeEnum } from "../../../../assets/enums";
  import CustomField from "../../../../genericComponents/customTextfield";
  import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
  import { useAppDispatch, useAppSelector } from "../../../../assets/hooks";
  import { setPopup } from "../../../../store/Slices/popupSlice";
  import { Country, State, City } from "country-state-city";
  import type { Event } from "../../../../types/types";
  import { editProfile } from "../../../../services";
  import { setPersonalInfo } from "../../../../store/Slices/personalInfoSlice";
  import Images from "../../../../assets/index";
  import * as Yup from 'yup';
  import ReusableDropdown from "src/genericComponents/reusableSelect";
  // import CityDropdown from "./reuseDropdowns";
  import SearchIcon from "@mui/icons-material/Search";
import { CustomButton } from "src/genericComponents/customButton";
import { globalButton } from "../../../../components/styles/customStyles";

  
  interface ICountry {
    name: string;
    isoCode: string;
  }
  interface IState {
    name: string;
    isoCode: string;
    countryCode: string;
  }
  interface ICity {
    name: string;
    stateCode: string;
    countryCode: string;
  }
  const Address = () => {
    const dispatch = useAppDispatch();
    const personalInfo = useAppSelector((state) => state.personalInfo);
    const [permanent, setPermanent] = useState("");
    const [loader, setLoader] = useState(false);
    const [countries, setCountries] = useState<ICountry[]>([]);
    const [states, setStates] = useState<IState[]>([]);
    const [cities, setCities] = useState<ICity[]>([]);
    const [permanentstates, setpermanentstates] = useState<IState[]>([]);
    const [permanentcities, setpermanentCities] = useState<ICity[]>([]);
    const [selectedCountry, setSelectedCountry] = useState<any>(
      personalInfo?.data?.temporaryAddress?.country
    );
    const [selectedState, setSelectedState] = useState<any>(
      personalInfo?.data?.temporaryAddress?.state
    );
    const [selectedCity, setSelectedCity] = useState<any>(
      personalInfo?.data?.temporaryAddress?.city
    );
    const [permanentCountry, setPermanentCountry] = useState<any>(
      personalInfo?.data?.permanentAddress?.country
    );
    const [permanentState, setpermanentState] = useState<any>(
      personalInfo?.data?.permanentAddress?.state
    );
    const [permanentCity, setpermanentCity] = useState<any>(
      personalInfo?.data?.permanentAddress?.city
    );
    const ITEM_HEIGHT = 78;
    const ITEM_PADDING_TOP = 8;
    const MenuProps = {
      PaperProps: {
        style: {
          maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
          width: 220,
        },
      },
    };
    const countryInfo = countries.find((c: any) => c.name === selectedCountry);
    const stateInfo = states.find((s: any) => s.name === selectedState);
    const countryPInfo = countries.find((c: any) => c.name === permanentCountry);
    const stateInPfo = states.find((s: any) => s.name === permanentState);
    useEffect(() => {
      const fetchCountries = async () => {
        const allCountries = await Country.getAllCountries();
        setCountries(allCountries);
      };
  
      fetchCountries();
    }, []);
  
    useEffect(() => {
      const fetchStateAndCities = async () => {
        const statesOfCountry = await State.getStatesOfCountry(
          countryInfo?.isoCode
        );
        setStates(statesOfCountry);
  
        const citiesOfState = await City.getCitiesOfState(
          stateInfo?.countryCode ?? "",
          stateInfo?.isoCode ?? ""
        );
        setCities(citiesOfState);
      };
      fetchStateAndCities();
    }, [countryInfo, stateInfo]);
  
    useEffect(() => {
      const fetchStateAndCities = async () => {
        const statesOfCountry = await State.getStatesOfCountry(
          countryPInfo?.isoCode
        );
        setpermanentstates(statesOfCountry);
  
        const citiesOfState = await City.getCitiesOfState(
          stateInPfo?.countryCode ?? "",
          stateInPfo?.isoCode ?? ""
        );
        setpermanentCities(citiesOfState);
      };
      fetchStateAndCities();
    }, [countryPInfo, stateInPfo]);
  
    useEffect(() => {
      if (personalInfo?.data) {
        setSelectedCountry(personalInfo?.data?.temporaryAddress?.country);
        setSelectedState(personalInfo?.data?.temporaryAddress?.state);
        setSelectedCity(personalInfo?.data?.temporaryAddress?.city);
  
        setPermanentCountry(personalInfo?.data?.permanentAddress?.country);
        setpermanentState(personalInfo?.data?.permanentAddress?.state);
        setpermanentCity(personalInfo?.data?.permanentAddress?.city);
      }
    }, [personalInfo]);
  
    const eventHandler = async (event: Event) => {
      switch (event.name) {
        case "temporaryCountry":
          setSelectedCountry(event.data);
          setSelectedState('');
          setSelectedCity('');
          setCities([]);
  
          const countryInfo = countries.find((c: any) => c.name === event.data);
          if (countryInfo) {
            const statesOfCountry = await State.getStatesOfCountry(countryInfo.isoCode);
            setStates(statesOfCountry);
          }
          break;
  
        case "temporaryState":
          setSelectedCity('');
          setSelectedState(event.data);
          const stateInfo = states.find((s: any) => s.name === event.data);
          if (stateInfo) {
            const citiesOfState = await City.getCitiesOfState(stateInfo.countryCode, stateInfo.isoCode);
            setCities(citiesOfState);
          }
          break;
  
        case "temporaryCity":
          setSelectedCity(event.data);
          break;
        case "permanentCountry":
          setPermanentCountry(event.data);
          setpermanentState("");
          setpermanentCity('');
          setpermanentCities([]);
          const countryPInfo = countries.find((c: any) => c.name === event.data);
          if (countryPInfo) {
            const statesOfCountry = await State.getStatesOfCountry(
              countryPInfo.isoCode
            );
            setpermanentstates(statesOfCountry);
          }
          break;
        case "permanentState":
          setpermanentCity('');
          setpermanentState(event.data);
          const statePInfo = permanentstates.find((s: any) => s.name === event.data);
          if (statePInfo) {
            const citiesOfState = await City.getCitiesOfState(
              statePInfo?.countryCode,
              statePInfo?.isoCode
            );
            setpermanentCities(citiesOfState);
          }
          break;
        case "permanentCity":
          setpermanentCity(event.data);
          break;
          default:
            break;
      }
   
  
    };
  
    // eslint-disable-next-line 
    const validationSchema = Yup.object({
      personalDetails: Yup.object({
        temporaryAddress: Yup.object({
          city: Yup.string().required('City is required'),
          state: Yup.string().required('State is required'),
          pinCode: Yup.string().required('Pincode is required'),
          country: Yup.string().required('Country is required'),
          addressLine1: Yup.string().required('Address Line1 is required'),
        }),
        permanentAddress: Yup.object().when('permanent', {
          is: 'no',
          then: schema => schema.shape({
            city: Yup.string().required('City is required'),
            state: Yup.string().required('State is required'),
            pinCode: Yup.string().required('Pincode is required'), // Changed to string
            country: Yup.string().required('Country is required'),
            addressLine1: Yup.string().required('Address Line1 is required'),
          }),
        }),
      }),
    });
  
    const initialValues = {
      personalDetails: {
        temporaryAddress: {
          city: selectedCity,
          state: selectedState,
          pinCode: personalInfo?.data?.temporaryAddress?.pinCode,
          country: selectedCountry,
          addressLine1: personalInfo?.data?.temporaryAddress?.addressLine1,
          addressLine2: personalInfo?.data?.temporaryAddress?.addressLine2,
          addressLine3: personalInfo?.data?.temporaryAddress?.addressLine3,
        },
        permanentAddress: {
          city: permanentCity,
          state: permanentState,
          pinCode: personalInfo?.data?.permanentAddress?.pinCode,
          country: permanentCountry,
          addressLine1: personalInfo?.data?.permanentAddress?.addressLine1,
          addressLine2: personalInfo?.data?.permanentAddress?.addressLine2,
          addressLine3: personalInfo?.data?.permanentAddress?.addressLine3,
        },
      },
    };
  
    function isEmpty(obj: any): boolean {
      if (obj == null) return true;
      return Object.keys(obj).length === 0 && obj.constructor === Object;
    }
  
    useEffect(() => {
      const tempAddress = initialValues.personalDetails.temporaryAddress;
      const permAddress = initialValues.personalDetails.permanentAddress;
      if (isEmpty(personalInfo?.data?.permanentAddress)) {
        setPermanent("");
      } else if (
        tempAddress.city === permAddress.city &&
        tempAddress.state === permAddress.state &&
        tempAddress.pinCode === permAddress.pinCode &&
        tempAddress.country === permAddress.country &&
        tempAddress.addressLine1 === permAddress.addressLine1 &&
        tempAddress.addressLine2 === permAddress.addressLine2 &&
        tempAddress.addressLine3 === permAddress.addressLine3
      ) {
        setPermanent("yes");
      } else {
        setPermanent("no");
      }
      // eslint-disable-next-line
    }, []);
  
    const submit = async (values: any) => {
      try {
        setLoader(true);
        if (permanent === "yes") {
          values.personalDetails.permanentAddress = {
            ...values.personalDetails.temporaryAddress,
          };
        }
        const response = await editProfile({
          personalDetails: {
          ...personalInfo.data,
          temporaryAddress:
          values?.personalDetails.temporaryAddress,
        permanentAddress:
          values?.personalDetails.permanentAddress,
        }
        });
        if (response.data.success) {
          dispatch(
            setPersonalInfo({
              ...personalInfo.data,
              temporaryAddress:
              response.data?.data?.personalDetails.temporaryAddress,
            permanentAddress:
              response.data?.data?.personalDetails.permanentAddress,
            })
          );
          dispatch(
            setPopup({
              show: true,
              data: {
                container: {
                  name: "success",
                  data: "Address added successfully",
                  dimensions: {
                    width: "30%",
                    height: "40%",
                    show: "none"
  
                  },
                },
                type: "custom",
              },
            })
          );
          setLoader(false);
        } else {
          setLoader(false);
        }
      } catch (error) { }
      
    };
    const editHandler=()=>{
      setFormEdit(true)
    }

    const [searchQuery, setSearchQuery] = useState("");
    const [formEdit,setFormEdit]=useState(false)
    const [permFormEdit, setPermFormEdit] = useState(false);

    const handleTempEdit = () => {
      setFormEdit(true);
    };
    const handlePermEdit = () => {
      setPermFormEdit(true);
    };
    const handleCancelTemp = () => {
      setFormEdit(false);
    };
    const handleCancelPerm = () => {
      setPermFormEdit(false);
    };
  
    const filteredCities = useMemo(
      () =>
        cities.filter((city) =>
          city.name.toLowerCase().includes(searchQuery.toLowerCase())
        ),
      [cities, searchQuery]
    );
    const permfilteredCities = useMemo(
      () =>
        permanentcities.filter((city) =>
          city.name.toLowerCase().includes(searchQuery.toLowerCase())
        ),
      [permanentcities, searchQuery]
    );
  
  
    return (   
      <>
        {permFormEdit ? (
          // Only show permanent address edit form
          <Formik
            initialValues={initialValues}
            validationSchema={validationSchema}
            enableReinitialize={true}
            onSubmit={(values: any) => submit(values)}
          >
            {({ touched, errors, values, handleChange, setFieldValue }) => {
              return (
                <Form style={{}}>
                  <Grid container spacing={4} sx={{ px: { xs: 0.5, lg: 2 }, width: "100% !important"  }}>
                    <Grid item xs={12} sm={6}>
                      <InputLabel id="permAddressLine1" sx={{ fontWeight: "500", color: "#000", mb: 1 }}>
                        Address Line 1
                      </InputLabel>
                      <CustomField
                        id="permAddressLine1"
                        type="text"
                        placeholder="Address Line 1"
                        name="personalDetails.permanentAddress.addressLine1"
                        value={values.personalDetails.permanentAddress.addressLine1}
                        onChange={(e:any) => setFieldValue("personalDetails.permanentAddress.addressLine1", e.target.value)}
                        fullWidth
                        error={Boolean((touched.personalDetails as any)?.permanentAddress?.addressLine1 && (errors.personalDetails as any)?.permanentAddress?.addressLine1)}
                      />
                      <div style={{ color: "#e60c0c", fontSize: "12px" }}>
                        <ErrorMessage name="personalDetails.permanentAddress.addressLine1" component="div" />
                      </div>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <InputLabel id="permAddressLine2" sx={{ fontWeight: "500", color: "#000", mb: 1 }}>
                        Address Line 2
                      </InputLabel>
                      <CustomField
                        id="permAddressLine2"
                        type="text"
                        placeholder="Address Line 2"
                        name="personalDetails.permanentAddress.addressLine2"
                        value={values.personalDetails.permanentAddress.addressLine2}
                        onChange={handleChange}
                        fullWidth
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <InputLabel id="permAddressLine3" sx={{ fontWeight: "500", color: "#000", mb: 1 }}>
                        Address Line 3
                      </InputLabel>
                      <CustomField
                        id="permAddressLine3"
                        type="text"
                        placeholder="Address Line 3"
                        name="personalDetails.permanentAddress.addressLine3"
                        value={values.personalDetails.permanentAddress.addressLine3}
                        onChange={handleChange}
                        fullWidth
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <InputLabel id="permCountry" sx={{ fontWeight: "500", color: "#000", mb: 1 }}>
                        Country
                      </InputLabel>
                      <ReusableDropdown
                        label="Country"
                        name="personalDetails.permanentAddress.country"
                        options={countries.map(country => country.name)}
                        value={permanentCountry}
                        onChange={(e: any) => eventHandler({
                          name: "permanentCountry",
                          data: e?.target.value,
                        })}
                        placeholder="Select"
                        searchEnabled={true}
                      />
                      <div style={{ color: "#e60c0c", fontSize: "12px" }}>
                        <ErrorMessage name="personalDetails.permanentAddress.country" component="div" />
                      </div>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <InputLabel id="permState" sx={{ fontWeight: "500", color: "#000", mb: 1 }}>
                        State/Province
                      </InputLabel>
                      <ReusableDropdown
                        label="State"
                        name="personalDetails.permanentAddress.state"
                        options={permanentstates.map(state => state.name)}
                        value={permanentState}
                        onChange={(e: any) => eventHandler({
                          name: "permanentState",
                          data: e?.target.value,
                        })}
                        placeholder="Select"
                        searchEnabled={true}
                      />
                      <div style={{ color: "#e60c0c", fontSize: "12px" }}>
                        <ErrorMessage name="personalDetails.permanentAddress.state" component="div" />
                      </div>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <InputLabel id="permCity" sx={{ fontWeight: "500", color: "#000", mb: 1 }}>
                        City/Town
                      </InputLabel>
                      <FormControl fullWidth>
                        <Select
                          id="permCity"
                          name="personalDetails.permanentAddress.city"
                          value={permanentCity}
                          displayEmpty
                          size="small"
                          defaultValue=""
                          onChange={(e) => setpermanentCity(e.target.value)}
                          fullWidth
                          IconComponent={ExpandMoreIcon}
                          onClose={() => setSearchQuery("")}
                          MenuProps={{
                            PaperProps: {
                              sx: {
                                maxHeight: 300,
                                "&.MuiPaper-root": {
                                  boxShadow: 3,
                                  borderRadius: "15px !important",
                                },
                              },
                            },
                          }}
                        >
                          <ListSubheader>
                            <TextField
                              size="small"
                              autoFocus
                              placeholder="Search City..."
                              fullWidth
                              value={searchQuery}
                              onChange={(e) => setSearchQuery(e.target.value)}
                              InputProps={{
                                startAdornment: (
                                  <InputAdornment position="start">
                                    <SearchIcon />
                                  </InputAdornment>
                                ),
                              }}
                              onKeyDown={(e) => {
                                if (e.key !== "Escape") {
                                  e.stopPropagation();
                                }
                              }}
                            />
                          </ListSubheader>
                          <MenuItem value="" disabled>
                            Select City
                          </MenuItem>
                          {permfilteredCities.length > 0 ? (
                            permfilteredCities.map((city: any) => (
                              <MenuItem key={city.name} value={city.name}>
                                {city.name}
                              </MenuItem>
                            ))
                          ) : (
                            <MenuItem disabled>No cities available</MenuItem>
                          )}
                          {!permanentcities.some((city) => city.name === permanentCity) &&
                            permanentCity !== undefined && permanentCity !== "" && (
                              <MenuItem key={permanentCity} value={permanentCity}>
                                {permanentCity}
                              </MenuItem>
                            )}
                          <MenuItem
                            sx={{
                              fontSize: "14px !important",
                              display: "flex",
                              alignItems: "center",
                              whiteSpace: "normal",
                              color: "#1cbeb5 !important",
                            }}
                            onClick={() => {
                              dispatch(
                                setPopup({
                                  show: true,
                                  data: {
                                    container: {
                                      name: "customfield",
                                      data: {
                                        name: "personalDetails.permanentAddress.city",
                                        fieldName: permanentCity,
                                        setFieldName: setpermanentCity,
                                        placeholder: "Enter your city",
                                      },
                                      dimensions: {
                                        width: "450px",
                                      },
                                    },
                                    type: "custom",
                                  },
                                })
                              );
                            }}
                          >
                            Can't find city
                          </MenuItem>
                        </Select>
                      </FormControl>
                      <div style={{ color: "#e60c0c", fontSize: "12px" }}>
                        <ErrorMessage name="personalDetails.permanentAddress.city" component="div" />
                      </div>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <InputLabel id="permPinCode" sx={{ fontWeight: "500", color: "#000", mb: 1 }}>
                        Zipcode
                      </InputLabel>
                      <CustomField
                        id="permPinCode"
                        type="number"
                        placeholder="Pincode"
                        name="personalDetails.permanentAddress.pinCode"
                        value={values.personalDetails.permanentAddress.pinCode}
                        onChange={handleChange}
                        fullWidth
                        error={Boolean((touched.personalDetails as any)?.permanentAddress?.pinCode && (errors.personalDetails as any)?.permanentAddress?.pinCode)}
                      />
                      <div style={{ color: "#e60c0c", fontSize: "12px" }}>
                        <ErrorMessage name="personalDetails.permanentAddress.pinCode" component="div" />
                      </div>
                    </Grid>
                  </Grid>
                  <Box
                    sx={{ display: "flex", justifyContent: "end", pb:2, mr: 10, gap: 2 }}
                  >
                    <Button variant="outlined" color="secondary" onClick={handleCancelPerm}>Cancel</Button>
                    <CustomButton
                      width="100px"
                      handleSubmit={() => submit}
                    >
                      {loader ? (
                        <img
                          src={Images.standardLoader}
                          alt="load"
                          style={{ width: "1rem",height:"1rem",padding:"6px" }}
                        />
                      ) : (
                        "Save"
                      )}
                    </CustomButton>
                  </Box>
                </Form>
              );
            }}
          </Formik>
        ) : (
          <>
            {/* Temporary address edit or view */}
            {formEdit ? (
              <Formik
                initialValues={initialValues}
                validationSchema={validationSchema}
                enableReinitialize={true}
                onSubmit={(values: any) => submit(values)}
              >
                {({ touched, errors, values, handleChange, setFieldValue }) => {
                  return (
                    <Form style={{}}>
                      <Grid container spacing={4} sx={{ px: { xs: 0.5, lg: 2 }, width: "100% !important"  }}>
                        <Grid item xs={12} sm={6}>
                          <InputLabel
                            id="firstName"
                            sx={{ fontWeight: "500", color: "#495052", mb: 1 }}
                          >
                            Address Line 1
                          </InputLabel>
                          <CustomField
                            id="addressLine1"
                            type="text"
                            placeholder="Address Line 1"
                            name="personalDetails.temporaryAddress.addressLine1"
                            value={values.personalDetails.temporaryAddress.addressLine1}
                            onChange={(e:any) =>
                              setFieldValue("personalDetails.temporaryAddress.addressLine1", e.target.value)
                            }
                            fullWidth
                            error={Boolean((touched.personalDetails as any)?.temporaryAddress?.addressLine1 && (errors.personalDetails as any)?.temporaryAddress?.addressLine1)}
                          />
                          <div style={{ color: "#e60c0c", fontSize: "12px" }}>
                            <ErrorMessage name="personalDetails.temporaryAddress.addressLine1" component="div" />
                          </div>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                          <InputLabel
                            id="firstName"
                            sx={{ fontWeight: "500", color: "#495052", mb: 1 }}
                          >
                            Address Line 2
                          </InputLabel>
                          <CustomField
                            id="addressLine2"
                            type="text"
                            placeholder="Address Line 2"
                            name="personalDetails.temporaryAddress.addressLine2"
                            value={values.personalDetails.temporaryAddress.addressLine2}
                            onChange={handleChange}
                            fullWidth
                          />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                          <InputLabel
                            id="firstName"
                            sx={{ fontWeight: "500", color: "#495052", mb: 1 }}
                          >
                            Address Line 3
                          </InputLabel>
                          <CustomField
                            id="addressLine3"
                            type="text"
                            placeholder="Address Line 3"
                            name="personalDetails.temporaryAddress.addressLine3"
                            value={values.personalDetails.temporaryAddress.addressLine3}
                            onChange={handleChange}
                            fullWidth
                          />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                          <InputLabel
                            id="firstName"
                            sx={{ fontWeight: "500", color: "#495052", mb: 1 }}
                          >
                            Country
                          </InputLabel>
                          <ReusableDropdown
                            label="Country"
                            name="personalDetails.temporaryAddress.country"
                            options={countries.map(country => country.name)}
                            value={selectedCountry}
                            onChange={(e: any) => eventHandler({
                              name: "temporaryCountry",
                              data: e?.target.value,
                            })}
                            placeholder="Select"
                            searchEnabled={true}
                          />
  
                          <div style={{ color: "#e60c0c", fontSize: "12px" }}>
                            <ErrorMessage name="personalDetails.temporaryAddress.country" component="div" />
                          </div>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                          <InputLabel
                            id="firstName"
                            sx={{ fontWeight: "500", color: "#495052", mb: 1 }}
                          >
                            State/Province
                          </InputLabel>
                          <ReusableDropdown
                            label="State"
                            name="personalDetails.temporaryAddress.state"
                            options={states.map(state => state.name)}
                            value={selectedState}
                            onChange={(e: any) => eventHandler({
                              name: "temporaryState",
                              data: e?.target.value,
                            })}
                            placeholder="Select"
                            searchEnabled={true}
                          />
                          <div style={{ color: "#e60c0c", fontSize: "12px" }}>
                            <ErrorMessage name="personalDetails.temporaryAddress.state" component="div" />
                          </div>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                          <InputLabel
                            id="firstName"
                            sx={{ fontWeight: "500", color: "#495052", mb: 1 }}
                          >
                            City/Town
                          </InputLabel>
                          <FormControl fullWidth>
                            <Select
                              id="city"
                              name="personalDetails.temporaryAddress.city"
                              value={selectedCity}
                              displayEmpty
                              size="small"
                              defaultValue=""
                              onChange={(e) => setSelectedCity(e.target.value)}
                              fullWidth
                              IconComponent={ExpandMoreIcon}
                              onClose={() => setSearchQuery("")} // Reset search when dropdown closes
                              MenuProps={{
                                PaperProps: {
                                  sx: {
                                    maxHeight: 300,
                                    "&.MuiPaper-root": {
                                      boxShadow: 3,
                                      borderRadius: "15px !important",
                                    },
                                  },
                                },
                              }}
                            >
                              <ListSubheader>
                                <TextField
                                  size="small"
                                  autoFocus
                                  placeholder="Search City..."
                                  fullWidth
                                  value={searchQuery}
                                  onChange={(e) => setSearchQuery(e.target.value)}
                                  InputProps={{
                                    startAdornment: (
                                      <InputAdornment position="start">
                                        <SearchIcon />
                                      </InputAdornment>
                                    ),
                                  }}
                                  onKeyDown={(e) => {
                                    if (e.key !== "Escape") {
                                      e.stopPropagation();
                                    }
                                  }}
                                />
                              </ListSubheader>
  
                              <MenuItem value="" disabled>
                                Select City
                              </MenuItem>
  
                              {filteredCities.length > 0 ? (
                                filteredCities.map((city: any) => (
                                  <MenuItem key={city.name} value={city.name}>
                                    {city.name}
                                  </MenuItem>
                                ))
                              ) : (
                                <MenuItem disabled>No cities available</MenuItem>
                              )}
  
                              {!cities.some((city) => city.name === selectedCity) &&
                                selectedCity !== undefined && selectedCity !== "" && (
                                  <MenuItem key={selectedCity} value={selectedCity}>
                                    {selectedCity}
                                  </MenuItem>
                                )}
  
                              <MenuItem
                                sx={{
                                  fontSize: "14px !important",
                                  display: "flex",
                                  alignItems: "center",
                                  whiteSpace: "normal",
                                  color: "#1cbeb5 !important",
                                }}
                                onClick={() => {
                                  dispatch(
                                    setPopup({
                                      show: true,
                                      data: {
                                        container: {
                                          name: "customfield",
                                          data: {
                                            name: "personalDetails.temporaryAddress.city",
                                            fieldName: selectedCity,
                                            setFieldName: setSelectedCity,
                                            placeholder: "Enter your city",
                                          },
                                          dimensions: {
                                            width: "450px",
                                          },
                                        },
                                        type: "custom",
                                      },
                                    })
                                  );
                                }}
                              >
                                Can't find city
                              </MenuItem>
                            </Select>
                          </FormControl>
  
                          <div style={{ color: "#e60c0c", fontSize: "12px" }}>
                            <ErrorMessage name="personalDetails.temporaryAddress.city" component="div" />
                          </div>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                          <InputLabel
                            id="firstName"
                            sx={{ fontWeight: "500", color: "#495052", mb: 1 }}
                          >
                            Zipcode
                          </InputLabel>
                          <CustomField
                            id="pinCode"
                            type="number"
                            placeholder="Pincode"
                            name="personalDetails.temporaryAddress.pinCode"
                            value={values.personalDetails.temporaryAddress.pinCode}
                            onChange={handleChange}
                            fullWidth
                            error={Boolean((touched.personalDetails as any)?.temporaryAddress?.pinCode && (errors.personalDetails as any)?.temporaryAddress?.pinCode)}
  
                          />
                          <div style={{ color: "#e60c0c", fontSize: "12px" }}>
                            <ErrorMessage name="personalDetails.temporaryAddress.pinCode" component="div" />
                          </div>
                        </Grid>
                        <Grid item xs={12} sm={6}></Grid>
                        <Grid item xs={12} sm={6}>
                          <InputLabel id="" sx={{ color: "#495052" }}>
                            Is your Home address same as Contact address?
                          </InputLabel>
                          <FormControl>
                            <RadioGroup
                              row
                              aria-labelledby="demo-row-radio-buttons-group-label"
                              name="row-radio-buttons-group"
                              value={permanent}
                              onChange={(e: any) => setPermanent(e.target.value)}
                            >
                              <FormControlLabel
                                value="yes"
                                control={
                                  <Radio
                                    size="small"
                                    sx={{
                                      "&.MuiRadio-root.Mui-checked": {
                                        color: "#3B3F76 !important",
                                      },
                                    }}
                                  />
                                }
                                label="Yes"
                              />
                              <FormControlLabel
                                value="no"
                                control={
                                  <Radio
                                    size="small"
                                    sx={{
                                      "&.MuiRadio-root.Mui-checked": {
                                        color: "#3B3F76 !important",
                                      },
                                    }}
                                  />
                                }
                                label="No"
                              />
                            </RadioGroup>
                          </FormControl>
                        </Grid>
                        <Grid item xs={12} sm={6}></Grid>
                        {permanent === "no" ? (
                          <>
                            <Grid item xs={12} sm={6}>
                              <Typography sx={{ color: '#8E9091', fontWeight: 500, mt: 4 }}>Address Line 1</Typography>
                              <Typography sx={{color:"#495052"}}>{personalInfo?.data?.permanentAddress?.addressLine1 || 'NA'}</Typography>
                              <Typography sx={{ color: '#8E9091', fontWeight: 500, mt: 2 }}>Address Line 2</Typography>
                              <Typography sx={{color:"#495052"}}>{personalInfo?.data?.permanentAddress?.addressLine2 || 'NA'}</Typography>
                              <Typography sx={{ color: '#8E9091', fontWeight: 500, mt: 2 }}>Country</Typography>
                              <Typography sx={{color:"#495052"}}>{personalInfo?.data?.permanentAddress?.country || 'NA'}</Typography>
                              <Typography sx={{ color: '#8E9091', fontWeight: 500, mt: 2 }}>State/Province</Typography>
                              <Typography sx={{color:"#495052"}}>{personalInfo?.data?.permanentAddress?.state || 'NA'}</Typography>
                            </Grid>
                            <Grid item xs={12} sm={6}>
                              <Typography sx={{ color: '#8E9091', fontWeight: 500, mt: 4 }}>Address Line 3</Typography>
                              <Typography sx={{color:"#495052"}}>{personalInfo?.data?.permanentAddress?.addressLine3 || 'NA'}</Typography>
                              <Typography sx={{ color: '#8E9091', fontWeight: 500, mt: 2 }}>City/Town</Typography>
                              <Typography sx={{color:"#495052"}}>{personalInfo?.data?.permanentAddress?.city || 'NA'}</Typography>
                              <Typography sx={{ color: '#8E9091', fontWeight: 500, mt: 2 }}>Zipcode</Typography>
                              <Typography sx={{color:"#495052"}}>{personalInfo?.data?.permanentAddress?.pinCode || 'NA'}</Typography>
                            </Grid>
                          </>
                        ) : null}
                        
                      </Grid>
                      <Box
                          sx={{ display: "flex", justifyContent: "end", pb:2, mr: 10, gap: 2 }}
                        >
                          <Button variant="outlined" color="secondary" onClick={handleCancelTemp}>Cancel</Button>
                          <CustomButton
                            width="100px"
                            handleSubmit={() => submit}
                          >
                            {loader ? (
                              <img
                                src={Images.standardLoader}
                                alt="load"
                                style={{ width: "1rem",height:"1rem",padding:"6px" }}
                              />
                            ) : (
                              "Save"
                            )}
                          </CustomButton>
                        </Box>
                    </Form>
                  );
                }}
              </Formik>
            ) : (
              <Box sx={{ boxShadow: 2, borderRadius: 3, background: '#fff', p: 3 }}>
                <Grid container spacing={2}>
                  <Grid item xs={12} sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                    <Typography variant="h6" sx={{ fontWeight: 600, color:"#495052" }}>
                      Address Details
                       </Typography>
                    <Button variant="outlined" sx={[{ border:'2px solid #F48438 ', borderRadius:'20px',color:'#495052' ,fontSize: { lg: "16px", xs: "12px" }, width: { lg: "100px", xs: "90px" }, padding: { lg: "8px", xs: "6px" } }]} onClick={handleTempEdit}>
                                 Edit
                               </Button>
                     </Grid>
                     <Grid item xs={12} sm={6}>
                    <Typography sx={{ color: '#8E9091', fontWeight: 500 }}>Address Line 1</Typography>
                    <Typography sx={{color:"#495052"}}>{personalInfo?.data?.temporaryAddress?.addressLine1 || 'NA'}</Typography>
                    <Typography sx={{ color: '#8E9091', fontWeight: 500, mt: 2 }}>Address Line 2</Typography>
                    <Typography sx={{color:"#495052"}}>{personalInfo?.data?.temporaryAddress?.addressLine2 || 'NA'}</Typography>
                    <Typography sx={{ color: '#8E9091', fontWeight: 500, mt: 2 }}>Country</Typography>
                    <Typography sx={{color:"#495052"}}>{selectedCountry || 'NA'}</Typography>
                    <Typography sx={{ color: '#8E9091', fontWeight: 500, mt: 2 }}>State/Province</Typography>
                    <Typography sx={{color:"#495052"}}>{selectedState || 'NA'}</Typography>
                     </Grid>
                     <Grid item xs={12} sm={6}>
                    <Typography sx={{ color: '#8E9091', fontWeight: 500 }}>Address Line 3</Typography>
                    <Typography sx={{color:"#495052"}}>{personalInfo?.data?.temporaryAddress?.addressLine3 || 'NA'}</Typography>
                    <Typography sx={{ color: '#8E9091', fontWeight: 500, mt: 2 }}>City/Town</Typography>
                    <Typography>{selectedCity || 'NA'}</Typography>
                    <Typography sx={{ color: '#8E9091', fontWeight: 500, mt: 2 }}>Zipcode</Typography>
                    <Typography sx={{color:"#495052"}}>{personalInfo?.data?.temporaryAddress?.pinCode || 'NA'}</Typography>
                     </Grid>
                     {/* Show permanent address if not same as contact address */}
                     {permanent === 'no' && !permFormEdit && (
                       <>
                         <Grid item xs={12} sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mt: 4 }}>
                           <Typography variant="h6" sx={{ fontWeight: 600, color:"#495052" }}>
                             Permanent Address
                           </Typography>
                           <Button variant="outlined" sx={[{ border:'2px solid #F48438 ', borderRadius:'20px',color:'#495052' ,fontSize: { lg: "16px", xs: "12px" }, width: { lg: "100px", xs: "90px" }, padding: { lg: "8px", xs: "6px" } }]} onClick={handlePermEdit}>
                             Edit
                           </Button>
                         </Grid>
                         <Grid item xs={12} sm={6}>
                           <Typography sx={{ color: '#8E9091', fontWeight: 500, mt: 4 }}>Address Line 1</Typography>
                           <Typography sx={{color:"#495052"}}>{personalInfo?.data?.permanentAddress?.addressLine1 || 'NA'}</Typography>
                           <Typography sx={{ color: '#8E9091', fontWeight: 500, mt: 2 }}>Address Line 2</Typography>
                           <Typography sx={{color:"#495052"}}>{personalInfo?.data?.permanentAddress?.addressLine2 || 'NA'}</Typography>
                           <Typography sx={{ color: '#8E9091', fontWeight: 500, mt: 2 }}>Country</Typography>
                           <Typography sx={{color:"#495052"}}>{personalInfo?.data?.permanentAddress?.country || 'NA'}</Typography>
                           <Typography sx={{ color: '#8E9091', fontWeight: 500, mt: 2 }}>State/Province</Typography>
                           <Typography sx={{color:"#495052"}}>{personalInfo?.data?.permanentAddress?.state || 'NA'}</Typography>
                         </Grid>
                         <Grid item xs={12} sm={6}>
                           <Typography sx={{ color: '#8E9091', fontWeight: 500, mt: 4 }}>Address Line 3</Typography>
                           <Typography sx={{color:"#495052"}}>{personalInfo?.data?.permanentAddress?.addressLine3 || 'NA'}</Typography>
                           <Typography sx={{ color: '#8E9091', fontWeight: 500, mt: 2 }}>City/Town</Typography>
                           <Typography sx={{color:"#495052"}}>{personalInfo?.data?.permanentAddress?.city || 'NA'}</Typography>
                           <Typography sx={{ color: '#8E9091', fontWeight: 500, mt: 2 }}>Zipcode</Typography>
                           <Typography sx={{color:"#495052"}}>{personalInfo?.data?.permanentAddress?.pinCode || 'NA'}</Typography>
                         </Grid>
                       </>
                     )}
                     </Grid>
              </Box>
            )}
          </>
        )}
      </>
    );
  };
  
  export default Address;
  