// import { Cancel } from "@mui/icons-material";
import {
  Autocomplete,
  Box,
  Button,
  // Button,
  FormControl,
  Grid,
  InputAdornment,
  InputLabel,
  MenuItem,
  Select,
  Slider,
  Stack,
  TextField,
  Typography,
} from "@mui/material";
import { Form, Formik } from "formik";
import React, { useEffect, useState } from "react";
import Images from "../../../../assets";
import { useAppDispatch, useAppSelector } from "../../../../assets/hooks";
import {
  budgetType,
  countryList,
  currencySymbols,
  degreeType,
  monthNames,
  skillsList,
  subDisciplines,
} from "../../../../assets/menu";
import { editProfile, requestAdvisor } from "../../../../services";
import { setPopup } from "../../../../store/Slices/popupSlice";
import { setpreferences } from "../../../../store/Slices/preferencesSlice";
import { setskills } from "../../../../store/Slices/skillsSlice";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { Advisor, Event } from "../../../../types/types";
import { CustomButton } from "../../../../genericComponents/customButton";
import * as Yup from "yup";
// import { useNavigate } from "react-router-dom";
import { addAdvisorInfo, updateAdvisorInfo } from "src/store/Slices/advisorsSlice";
import { addChats } from "src/store/Slices/chatsSlice";
import { secondaryButton } from "src/components/styles/customStyles";
import { budgets } from "src/assets/constants/misc";
import { globalButton } from "src/components/styles/customStyles";

// interface CurrencyOption {
//   label: string;
//   value: string;
// }
const Preferences = () => {
  const Preferences = useAppSelector((state) => state.preferences);
  const educationDetails=useAppSelector((state)=>state.educationhistory)
  console.log("educationDetails",educationDetails)
  const dispatch = useAppDispatch();
  const [loading, setLoading] = useState(false);
  const [intake, setIntake] = useState<string>(Preferences.data?.intake ? Preferences.data?.intake :"")
  const date = new Date(intake)
  // const monthDate = date.getMonth()
  const [year, setYear] = useState(Preferences.data?.intake ? date.getFullYear() : "")
  const [month, setMonth] = useState<any>(Preferences.data?.intake ? date.getMonth() : "")
  const [intakeError, setIntakeError] = useState("");
  const [courseData, setCourseData] = useState(
    Preferences?.data?.subCategory || []
  );
  const [countryData, setCountryData] = useState(
    Preferences?.data?.country || []
  );
  // const [minBudget, setMinBudget] = useState<number>(
  //   Preferences?.data?.budget?.lower || 0
  // );
  // const [maxBudget, setMaxBudget] = useState<number>(
  //   Preferences?.data?.budget?.upper || 5000000
  // );
  const [selectedCurrency, setSelectedCurrency] = useState<any>(
    Preferences?.data?.currency
  );

  const advisors = useAppSelector((state) => state.advisor);
  // const navigate = useNavigate();
  const assignedCountries =
    advisors?.data?.flatMap((advisor: Advisor) => advisor.assignedCountries) ||
    [];
  const unassignedCountries = Preferences?.data?.country?.filter(
    (country: string) => !assignedCountries.includes(country)
  );
  // eslint-disable-next-line
  const validationSchema = Yup.object().shape({
    preference: Yup.object().shape({
      intake: Yup.string()
        .required("Intake is required")
        .matches(/^(January|February|March|April|May|June|July|August|September|October|November|December)-\d{4}$/, "Month & Year required"),
      budget: Yup.object().shape({
        lower: Yup.number()
          .required("Lower budget is required")
          .min(0, "Lower budget must be greater than or equal to 0"),
        upper: Yup.number()
          .required("Upper budget is required")
          .moreThan(Yup.ref("lower"), "Upper budget must be greater than lower budget"),
      }),
      degree: Yup.string()
        .required("Degree is required")
        .oneOf(degreeType, "Invalid degree program selected"),
      country: Yup.array()
        .of(Yup.string())
        .min(1, "At least one country is required")
        .required("Countries are required"),
        subCategory: Yup.array()
        .of(Yup.string())
        .min(1, "At least one course is required")
        .required("Courses are required"),
        currency: Yup.string().required("Currency is required"),
    }),
  });
  

  const ITEM_HEIGHT = 48;
  const ITEM_PADDING_TOP = 8;
  const MenuProps = {
    PaperProps: {
      style: {
        maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
        width: 150,
      },
    },
  };

  const currencies = currencySymbols.map((symbol) => {
    const [label, value] = symbol.split("-");
    return { label: symbol, value };
  });

  // const handleOptionChange = (
  //   event: React.ChangeEvent<{}>,
  //   newValue: string[]
  // ) => {
  //   setSkillsData(newValue);
  // };

  const eventHandler = async (event: Event) => {
    switch (event.name) {
      case "OptionChange":
        setSelectedCurrency(event.data.newValue?.value);
        break;
      // case "sliderChange":
      //   if (Array.isArray(event.data.newValue)) {
      //     setMinBudget(event.data.newValue[0]);
      //     setMaxBudget(event.data.newValue[1]);
      //   }
      //   break;
      case "countryChange":
        setCountryData(event.data.newValue);
        break;
      case "courseChange":
        setCourseData(event.data.newValue);
        break;
    }
  };
  useEffect(() => {
    if (month !== "" && year !== "") {
      setIntake(`${monthNames[month]}-${year}`);
    } else {
      setIntake("");
    }
  }, [month, year]);

  useEffect(() => {
    if (year !== "" && month !== "") {
      setIntakeError("");
    }
  }, [year, month]);

  const initialValues = {
    preference: {
      intake: intake,
      budget:budgets.find((item)=>item.lower===Preferences.data?.budget?.lower && item.upper===Preferences.data?.budget.upper)?.name,
      degree: Preferences?.data?.degree,
      country: countryData,
      subCategory: courseData,
      currency: selectedCurrency,
    },
  };

  const submit = async (values: any) => {
    // Intake validation: year and month must be selected
    if (year === "" || month === "") {
      setIntakeError("Both year and month are required.");
      setLoading(false);
      return;
    } else {
      setIntakeError("");
    }
    let budget=budgets.find((item)=>item.name===values.preference.budget)
    setLoading(true);
    console.log("Submit",{...values,preference:{...values.preference,budget:{upper:budget?.upper,lower:budget?.lower}}})
    try {
      const response = await editProfile({...values,preference:{...values.preference,budget:{upper:budget?.upper,lower:budget?.lower}}});
      if (response) {
        dispatch(setpreferences(response.data.data.preference));
        dispatch(
          setPopup({
            show: true,
            data: {
              container: {
                name: "success",
                data: "Preferences updated ",
                dimensions: {
                  width: "30%",
                  height: "40%",
                  show: "none"

                },
              },
              type: "custom",
            },
          })
        );
        setLoading(false);
      }
    } catch (error) {
      setLoading(false);
    }
  };

  const addAdvisors = async (item: string) => {
    // Validate country input
    if (!item || typeof item !== 'string' || item.trim() === '') {
      console.error("Invalid country value:", item);
      dispatch(
        setPopup({
          show: true,
          data: {
            container: {
              name: "error",
              data: "Invalid country selected. Please try again.",
              dimensions: {
                width: "30%",
                height: "40%",
                show: "none"
              },
            },
            type: "custom",
          },
        })
      );
      return;
    }

    try {
      const countryPayload = {
        country: item.trim(), // Trim whitespace
      };

      console.log("Requesting advisor with payload:", countryPayload);

      const response = await requestAdvisor(countryPayload);

      // Check if response is valid and has the expected structure
      if (response && response.data) {
        // If response has data.data structure (new format)
        if (response.data.data && response.data.data.advisor) {
          const newAdvisor = response.data.data.advisor;
          const existingAdvisor = advisors?.data?.find((advisor) => advisor._id === newAdvisor._id);

          if (existingAdvisor) {
            dispatch(updateAdvisorInfo(newAdvisor));
          } else {
            dispatch(addAdvisorInfo(newAdvisor));
          }
          if (response.data.data.chat !== null) {
            dispatch(addChats(response.data.data.chat));
          }
        }
        // Note: Success popup is already shown by requestAdvisor service
      }
    } catch (err: any) {
      // Error popup is already shown by handleApiError in the service
      // Log detailed error for debugging
      console.error("Error requesting advisor:", {
        error: err,
        country: item,
        errorType: typeof err,
        errorMessage: err?.message || err,
      });
    }
  };

  const [formEdit, setFormEdit] = useState(false);

  return (
    <>
      {formEdit ? (
        <Box sx={{ px: { lg: 3 } }}>
      <Formik
        initialValues={initialValues}
        enableReinitialize={true}
        // validationSchema={validationSchema}
        onSubmit={(values: any) => submit(values)}
      >
        {({ touched, errors, values, handleChange, setFieldValue }) => (
          <Form className="mt-5">
            <Grid container spacing={4} sx={{ width:"100% !important" }}>
              <Grid
                item
                xs={12}
                md={6}
                sx={{ display: "flex", flexDirection: "column" }}
              >
                <Typography fontWeight="500" gutterBottom sx={{color:"#8E9091"}}
                >
                  What program would you like to pursue?
                </Typography>
                <Autocomplete
                  multiple
                  id="subdisciplines"
                  sx={{
                    "& .MuiOutlinedInput-root .MuiAutocomplete-input": {
                      padding: "5px",
                    },
                    width: "100%",
                  }}
                  limitTags={2}
                  size="small"
                  options={subDisciplines}
                  getOptionLabel={(option) => option}
                  value={courseData}
                  filterSelectedOptions
                  ListboxProps={{
                    style: { maxHeight: "180px", overflowY: "auto" },
                  }}
                  onChange={(event, newValue) =>
                    eventHandler({
                      name: "courseChange",
                      data: { event, newValue },
                    })
                  }
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      placeholder="Search programs"
                      variant="outlined"
                      InputProps={{
                        ...params.InputProps,
                      }}
                    />
                  )}
                />
                    {((touched.preference as any)?.subCategory && (errors.preference as any)?.subCategory) ? (
                      <div className="error">{(errors.preference as any)['subCategory']}</div>
                ) : null}
              </Grid>
              <Grid
                item
                xs={12}
                md={6}
                sx={{ display: "flex", flexDirection: "column" }}
              >
               <Typography fontWeight="500" gutterBottom sx={{color:"#8E9091"}}
                >
                  What are your preferred study destinations?
                </Typography>
                <Autocomplete
                  multiple
                  id="autocomplete"
                  sx={{
                    "& .MuiOutlinedInput-root .MuiAutocomplete-input": {
                      padding: "5px",
                    },
                    width: "100%",
                  }}
                  limitTags={2}
                  size="small"
                  options={countryList}
                  getOptionLabel={(option) => option}
                  value={countryData}
                  filterSelectedOptions
                  ListboxProps={{
                    style: { maxHeight: "180px", overflowY: "auto" },
                  }}
                  onChange={(event, newValue) =>
                    eventHandler({
                      name: "countryChange",
                      data: { event, newValue },
                    })
                  }
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      placeholder="Search country"
                      variant="outlined"
                      InputProps={{
                        ...params.InputProps,
                      }}
                    />
                  )}
                />
                    {((touched.preference as any)?.country && (errors.preference as any)?.country) ? (
                      <div className="error">{(errors.preference as any)['country']}</div>
                ) : null}
                {unassignedCountries?.map((item, i) => (
                  <Grid item xs={12} key={i}>
                    <Box
                      sx={{
                        py: 1,
                        display: "flex",
                        gap: "1rem",
                        alignItems: "center",
                      }}
                    >
                      <Button
                        onClick={(e) => {
                          e.preventDefault();
                          e.stopPropagation();
                          addAdvisors(item);
                        }}
                        type="button"
                        sx={{
                          border: "1px solid #3B3F76CC",
                          borderRadius: "15px",
                          p: 0.5,
                          textTransform: "none",
                          color: "#3B3F76CC",
                          "&:hover": {
                            backgroundColor: "#3B3F7610",
                            borderColor: "#3B3F76",
                          }
                        }}
                      >
                        Request advisor for {item}
                      </Button>
                    </Box>
                  </Grid>
                ))}
              </Grid>
              <Grid
                item
                xs={12}
                md={6}
                sx={{ display: "flex", flexDirection: "column" }}
              >
               <Typography fontWeight="500" gutterBottom sx={{color:"#8E9091"}}
                >
                  Which degree program do you prefer?
                </Typography>
                <FormControl size="small">
                  <InputLabel sx={{color:"#8E9091"}} id="degree">Select Degree</InputLabel>
                  <Select
                    labelId="degree"
                    id="degree"
                    size="small"
                    name="preference.degree"
                    MenuProps={MenuProps}
                    value={values.preference.degree}
                    label="Select Degree"
                    onChange={handleChange}
                  >
                    {degreeType.map((item, i) => (
                      <MenuItem value={item} key={i}>
                        {item}
                      </MenuItem>
                    ))}
                  </Select>
                      {((touched.preference as any)?.degree && (errors.preference as any)?.degree) ? (
                        <div className="error">{(errors.preference as any)['degree']}</div>
                  ) : null}
                </FormControl>
              </Grid>
              <Grid item xs={12} md={6} >
                <Typography fontWeight="500" gutterBottom sx={{color:"#8E9091"}}
                >
                  When would you like to begin your studies?
                </Typography>
                <div style={{ display: "flex", gap: "1rem" }}>
                  <Select
                    labelId="degree"
                    id="degree"
                    size="small"
                    // name="preference.degree"
                    MenuProps={MenuProps}
                    value={year}
                    // label="Select Year"
                    onChange={(e: any) => setYear(e.target.value)}
                    defaultValue=""
                    displayEmpty
                  >
                    <MenuItem sx={{color:"#8E9091"}} value="" disabled>Select Year</MenuItem>
                    {["2025", "2026", "2027", "2028", "2029", "2030"].map((item, i) => (
                      <MenuItem value={item} key={i}>
                        {item}
                      </MenuItem>
                    ))}
                  </Select>
                  <Select
                    labelId="degree"
                    id="degree"
                    size="small"
                    // name="preference.degree"
                    MenuProps={MenuProps}
                    value={month}
                    // label="Select Year"
                    onChange={(e: any) => setMonth(e.target.value)}
                    defaultValue=""
                    displayEmpty
                  >
                    <MenuItem sx={{color:"#8E9091"}} value="" disabled>Select Month</MenuItem>
                    {monthNames.map((item, i) => (
                      <MenuItem value={i} key={i}>
                        {item}
                      </MenuItem>
                    ))}
                  </Select>
                </div>
                {intakeError && (
                  <div className="error" style={{ color: 'red', marginTop: 4 }}>{intakeError}</div>
                )}
              </Grid>
              <Grid
                item
                xs={12}
                md={6}
                sx={{ display: "flex", flexDirection: "column" }}
              >
               <Typography sx={{color:"#8E9091"}} fontWeight="500" gutterBottom 
                >
                  What is your estimated budget range for your studies?
                </Typography>
                <FormControl size="small">
                  <InputLabel sx={{color:"#8E9091"}} id="budget">Select Budget</InputLabel>
                  <Select
                    labelId="budget"
                    id="budget"
                    size="small"
                    name="preference.budget"
                    MenuProps={MenuProps}
                    value={values.preference.budget}
                    label="Select budget"
                    onChange={handleChange}
                  >
                    {budgets.map((item, i) => (
                      <MenuItem value={item.name} key={item.id}>
                        {item.name}
                      </MenuItem>
                    ))}
                  </Select>
                      {((touched.preference as any)?.budget && (errors.preference as any)?.budget) ? (
                        <div className="error">{(errors.preference as any)['budget']}</div>
                  ) : null}
                </FormControl>
              </Grid>
              <Grid item xs={12} md={6}>
                <InputLabel
                  id="degree"
                  sx={{ fontWeight: "500", color: "#8E9091", mb: 0.5 }}
                >
                  Preferred currency?
                </InputLabel>
                <Autocomplete
                  sx={{
                    marginTop: "9px",
                    width: "100%",
                    "& .MuiInputBase-input": {
                      fontSize: "14px !important",
                    },
                    "&.MuiAutocomplete-listbox": {
                      width: "400px",
                    },
                    "&.MuiAutocomplete-listbox .MuiAutocomplete-option ": {
                      fontSize: "14px !important",
                    },
                    "& .MuiOutlinedInput-root": {
                      paddingRight: "0 !important",
                    },
                  }}
                  options={currencies}
                  getOptionLabel={(option) => option?.label}
                  value={
                    currencies.find(
                      (option) => option.value === selectedCurrency
                    ) || null
                  }
                  onChange={(
                    event: any,
                    newValue: { label: string; value: string } | null
                  ) =>
                    eventHandler({
                      name: "OptionChange",
                      data: { event, newValue },
                    })
                  }
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      placeholder="Search currency"
                      size="small"
                      InputProps={{
                        ...params.InputProps,
                        type: "search",
                        endAdornment: (
                          <ExpandMoreIcon
                            sx={{ color: "gray", marginRight: "5px" }}
                          />
                        ),
                        style: {
                          fontSize: "14px !important",
                          color: "#000 !important",
                        },
                      }}
                    />
                  )}
                />
                    {((touched.preference as any)?.currency && (errors.preference as any)?.currency) ? (
                      <div className="error">{String((errors.preference as any)['currency'])}</div>
                ) : null}
              </Grid>


              <Grid
                item
                xs={12}
                sx={{ justifyContent: "end", display: "flex" }}
              >
                <CustomButton
                  width="100px"
                  handleSubmit={() => console.log("first")}
                >
                  {loading ? (
                    <img
                      src={Images.standardLoader}
                      alt="load"
                      style={{ width: "1rem" }}
                    />
                  ) : (
                    "Save"
                  )}
                </CustomButton>
              </Grid>
            </Grid>
          </Form>
        )}
      </Formik>
        </Box>
      ) : (
        <Box sx={{ boxShadow: 2, borderRadius: 3, background: '#fff', p: 3, mb: 3 }}>
          <Grid container spacing={2}>
            <Grid item xs={12} sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
              <Typography variant="h6" sx={{ fontWeight: 600,color:"#8E9091" }}>
                Preferences
              </Typography>
              <Button variant="outlined" sx={[{ border:'2px solid #F48438 ', borderRadius:'20px',color:'#495052' ,fontSize: { lg: "16px", xs: "12px" }, width: { lg: "100px", xs: "90px" }, padding: { lg: "8px", xs: "6px" } }]} onClick={() => setFormEdit(true)}>
                Edit
              </Button>
            </Grid>
            <Grid item xs={12} sm={6}>
              <Typography sx={{ color: "#8E9091", fontWeight: 500 }}>Preferred Programs</Typography>
              <Typography sx={{color:"#495052"}}>{courseData && courseData.length > 0 ? courseData.join(', ') : 'NA'}</Typography>
              <Typography sx={{ color: "#8E9091", fontWeight: 500, mt: 2 }}>Preferred Study Destinations</Typography>
              <Typography sx={{color:"#495052"}}>{countryData && countryData.length > 0 ? countryData.join(', ') : 'NA'}</Typography>
              <Typography sx={{ color: "#8E9091", fontWeight: 500, mt: 2 }}>Degree Program</Typography>
              <Typography sx={{color:"#495052"}}>{Preferences?.data?.degree || 'NA'}</Typography>
              <Typography sx={{ color: "#8E9091", fontWeight: 500, mt: 2 }}>Intake</Typography>
              <Typography sx={{color:"#495052"}}>{intake || 'NA'}</Typography>
            </Grid>
            <Grid item xs={12} sm={6}>
              <Typography sx={{ color: "#8E9091", fontWeight: 500 }}>Budget</Typography>
              <Typography sx={{color:"#495052"}}>{Preferences?.data?.budget ? budgets.find((item) => item.lower === Preferences?.data?.budget?.lower && item.upper === Preferences?.data?.budget?.upper)?.name || 'NA' : 'NA'}</Typography>
              <Typography sx={{ color: "#8E9091", fontWeight: 500, mt: 2 }}>Currency</Typography>
              <Typography sx={{color:"#495052"}}>{selectedCurrency || 'NA'}</Typography>
            </Grid>
          </Grid>
    </Box>
      )}
    </>
  );
};

