import React, { useRef, useState, useEffect } from "react";
import {
  Button,
  Grid,
  TextField,
  Box,
  InputLabel,
  Stack,
  MenuItem,
  Select,
  FormControlLabel,
  Radio,
  FormControl,
  RadioGroup,
  Typography,
  Tooltip,
} from "@mui/material";
import Images from "../../../../assets";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { CustomButton } from "../../../../genericComponents/customButton";
import { useAppDispatch, useAppSelector } from "../../../../assets/hooks";
import { Event, WorkExperience } from "../../../../types/types";
import { setWorkExperience } from "../../../../store/Slices/workexperienceSlice";
import { editProfile, uploadProfile, getProfile } from "../../../../services";
import { setPopup } from "../../../../store/Slices/popupSlice";
import { setDocuments } from "../../../../store/Slices/documentsSlice";
import FileUploadOutlinedIcon from "@mui/icons-material/FileUploadOutlined";
import { allowedFileTypes, industrySector, workTypes } from "../../../../assets/menu";
import { secondaryButton } from "../../../../components/styles/customStyles";
import { Info } from "@mui/icons-material";
import VisibilityOutlinedIcon from '@mui/icons-material/VisibilityOutlined';
import DatePickerComponent from "src/genericComponents/datePicker";
import ConfirmDialog from "src/genericComponents/modalPopup/confirmDialog";
import { globalButton } from "../../../../components/styles/customStyles";

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 150,
    },
  },
};

const WorkExperiences = () => {
  const dispatch = useAppDispatch();
  const [open, setOpen] = useState<boolean>(false);
  const [deleteItem, setDeleteItem] = useState<any | null>(null);
  const handleDeleteOpen = (item: any) => {
    setOpen(true);
    setDeleteItem(item);
  };
  const [document, setdoc] = useState<any>(null);
  const inputFile = useRef<HTMLInputElement>(null);
  const [loading, setLoading] = useState(false);
  const [load, setLoad] = useState(false);
  const [formEdit, setFormEdit] = useState(false);

  const workExperienceData = useAppSelector((state) => state.workexperience);
  const [WorkExperienceDetails, setWorkExperienceDetails] = useState<WorkExperience[]>(
    workExperienceData.data?.length > 0
      ? workExperienceData.data
      : [
        {
          companyName: "",
          sector: "",
          type: "",
          startDate: "",
          endDate: "",
          designation: "",
          docId: document,
        },
      ]
  );
  const [workStatus, setWorkStatus] = useState<("completed" | "ongoing" | "")[]>(
    WorkExperienceDetails.map((work) =>
      work.endDate ? "completed" : ""
    )
  );
  const [errors, setErrors] = useState<Record<number, Record<string, string>>>({});

  useEffect(() => {
    async function fetchWorkExperience() {
      try {
        const profile = await getProfile();
        console.log('Fetched profile:', profile);
        if (profile && profile.data && profile.data.workExperience) {
          dispatch(setWorkExperience(profile.data.workExperience));
        }
      } catch (error) {
        // Optionally handle error
      }
    }
    fetchWorkExperience();
  }, [dispatch]);

  const validateForm = () => {
    const newErrors: Record<number, Record<string, string>> = {};
    WorkExperienceDetails.forEach((detail, index) => {
      const detailErrors: Record<string, string> = {};
      if (!detail.companyName) {
        detailErrors.companyName = "Company Name is required";
      }
      if (!detail.sector) {
        detailErrors.sector = "Sector is required";
      }
      if (!detail.designation) {
        detailErrors.designation = "Designation is required";
      }
      if (!detail.startDate) {
        detailErrors.startDate = "Start Date is required";
      }
      if (workStatus[index] === "completed" && !detail.endDate) {
        detailErrors.endDate = "End Date is required";
      }
      if (!detail.type) {
        detailErrors.type = "Work Type is required";
      }
      if (!detail.docId && !loading) {
        detailErrors.docId = "Document upload is required";
      }
      if (Object.keys(detailErrors).length > 0) {
        newErrors[index] = detailErrors;
      }
    });
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const eventHandler = async (event: Event) => {
    switch (event.name) {
      case "addWork":
        setWorkExperienceDetails([
          ...WorkExperienceDetails,
          {
            companyName: "",
            sector: "",
            type: "",
            designation: "",
            startDate: "",
            endDate: "",
            docId: document,
          },
        ]);
        setErrors({ ...errors, [WorkExperienceDetails.length]: {} });
        break;
      case "removeWorkExperience":
        const newWorkExperienceDetails = [...WorkExperienceDetails];
        newWorkExperienceDetails.splice(event.data, 1);
        // setWorkExperienceDetails(newWorkExperienceDetails);
        const newErrors = { ...errors };
        delete newErrors[event.data];
        setErrors(newErrors);
        try {
          let workexperience = {
            workExperience: newWorkExperienceDetails,
          };
          const response = await editProfile(workexperience);
          if (response) {
            dispatch(setWorkExperience(response.data.data.workExperience));
            setWorkExperienceDetails(response.data.data.workExperience?.length > 0 ? response.data.data.workExperience :[
              {
                companyName: "",
                sector: "",
                type: "",
                designation: "",
                startDate: "",
                endDate: "",
                docId: document,
              },
            ])
            dispatch(
              setPopup({
                show: true,
                data: {
                  container: {
                    name: "success",
                    data: "Work experience removed",
                    dimensions: {
                      width: "30%",
                      height: "40%",
                      show: "none"

                    },
                  },
                  type: "custom",
                },
              })
            );
          }
        } catch (error) { }
        break;
      case "inputChange":
        const { name, value } = event.data.e.target;
        setWorkExperienceDetails((prevDetails) => {
          const newDetails = [...prevDetails];
          newDetails[event.data.index] = {
            ...newDetails[event.data.index],
            [name]: value,
          };
          return newDetails;
        });
        setErrors((prevErrors) => {
          const newErrors = { ...prevErrors };
          if (newErrors[event.data.index]) {
            delete newErrors[event.data.index][name];
            if (Object.keys(newErrors[event.data.index]).length === 0) {
              delete newErrors[event.data.index];
            }
          }
          return newErrors;
        });
        break;
      case "inputClick":
        inputFile?.current?.click();
        break;
      case "statusChange":
        const newWorkStatus = [...workStatus];
        newWorkStatus[event.data.index] = event.data.e.target.value;
        setWorkStatus(newWorkStatus);
        break;
      case "submitForm":
        if (validateForm()) {
          setLoad(true)
          try {
            let workexperience = {
              workExperience: WorkExperienceDetails,
            };
            const response = await editProfile(workexperience);
            if (response) {
              dispatch(setWorkExperience(response.data.data.workExperience));
              dispatch(
                setPopup({
                  show: true,
                  data: {
                    container: {
                      name: "success",
                      data: "Work experience added",
                      dimensions: {
                        width: "30%",
                        height: "40%",
                        show: "none"

                      },
                    },
                    type: "custom",
                  },
                })
              );
            }
            setLoad(false)
          } catch (error) { 
            setLoad(false)
          }
        }
        break;
      case "documentUpload":
        const file = event.data.e.target.files && event.data.e.target.files[0];
        if (file && allowedFileTypes.includes(file.type)) {
          try {
            if (file !== null && file !== undefined) {
              setLoading(true);
              const formData = new FormData();
              formData.append("uploaded_file", file);
              formData.append("fieldPath", "workExperiences");
              formData.append("fileIdentifier", `workExperiences${file.name}`);
              const response = await uploadProfile(formData);
              if (response.data.success) {
                dispatch(setDocuments(response.data.data.docs));
                const lastDoc = response.data.data.docs.workExperiences[response.data.data.docs.workExperiences.length - 1];
                setdoc(lastDoc);
                const updatedWorkExperienceDetails = [...WorkExperienceDetails];
                updatedWorkExperienceDetails[WorkExperienceDetails.length - 1].docId = lastDoc;
                setWorkExperienceDetails(updatedWorkExperienceDetails);
                setLoading(false);
              } else {
                setLoading(false);
              }
            } else {
              setLoading(false);
            }
          } catch (err) {
            setLoading(false);
          }
        } else {
          dispatch(
            setPopup({
              show: true,
              data: {
                container: {
                  name: "error",
                  data: "Only jpeg , png , pdf files are accepted",
                  dimensions: {
                    width: "250px",
                    show: "none"

                  },
                },
                type: "custom",
              },
            })
          );
        }
        break;
    }
  };

  function getTotalWorkExperienceInMonths() {
    return WorkExperienceDetails.reduce((total, exp, idx) => {
      if (exp.startDate && (exp.endDate || workStatus[idx] === "completed")) {
        const start = new Date(exp.startDate);
        const end = exp.endDate
          ? new Date(exp.endDate)
          : new Date(); // If ongoing, use current date
        const months =
          (end.getFullYear() - start.getFullYear()) * 12 +
          (end.getMonth() - start.getMonth());
        return total + (months > 0 ? months : 0);
      }
      return total;
    }, 0);
  }

  return (
    <>
      {formEdit ? (
        <Box sx={{ overflowY: "scroll", px: 3, height: "75vh" }}>
          <Typography fontSize="1rem" gutterBottom color="#8E9091" sx={{ ml: 2, mb: 2, fontWeight: "500" }}>Provide details of your most recent or current experience</Typography>
          {WorkExperienceDetails?.map((WorkExperienceMember, index) => (
            <div className="mb-5" key={index}>
              <Grid container spacing={3} sx={{ px: 2 }}>
                <Grid item xs={12} sm={6}>
                  <InputLabel id="" sx={{ color: "#8E9091", fontWeight: 500 }}>
                    Name of Organization
                  </InputLabel>
                  <TextField
                    size="small"
                    name={`companyName`}
                    placeholder="Name of Organization"
                    value={WorkExperienceMember.companyName}
                    onChange={(e: any) =>
                      eventHandler({
                        name: "inputChange",
                        data: { index, e },
                      })
                    }
                    required
                    fullWidth
                    error={!!errors[index]?.companyName}
                    helperText={errors[index]?.companyName}
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel id="" sx={{ color: "#8E9091", fontWeight: 500 }}>
                    Sector
                  </InputLabel>
                  <Select
                    size="small"
                    id="Country"
                    name={`sector`}
                    displayEmpty
                    value={WorkExperienceMember.sector || ""}
                    onChange={(e: any) =>
                      eventHandler({
                        name: "inputChange",
                        data: { index, e },
                      })
                    }
                    IconComponent={ExpandMoreIcon}
                    required
                    fullWidth
                    error={!!errors[index]?.sector}
                    MenuProps={MenuProps}
                  >
                    <MenuItem sx={{color:"#495052"}} disabled value="">
                      Select a sector
                    </MenuItem>
                    {industrySector.map((sector) => (
                      <MenuItem sx={{color:"#495052"}} key={sector.name} value={sector.value}>
                        {sector.name}
                      </MenuItem>
                    ))}

                    {WorkExperienceMember.sector &&
                      !industrySector.some((sector) => sector.value === WorkExperienceMember.sector) && (
                        <MenuItem key={WorkExperienceMember.sector} value={WorkExperienceMember.sector}>
                          {WorkExperienceMember.sector}
                        </MenuItem>
                      )
                    }

                    <MenuItem
                      sx={{
                        fontSize: "14px !important",
                        display: "flex",
                        alignItems: "center",
                        whiteSpace: "normal",
                        color: "#1cbeb5  !important",
                      }}
                      onClick={() => {
                        dispatch(
                          setPopup({
                            show: true,
                            data: {
                              container: {
                                name: "customfield",
                                data: {
                                  name: `workExperience[${index}].sector`,
                                  fieldName: WorkExperienceMember.sector,
                                  setFieldName: (value: any) => {
                                    const newWorkExperience = [...WorkExperienceDetails];
                                    newWorkExperience[index].sector = value;
                                    setWorkExperienceDetails(newWorkExperience);
                                  },
                                  placeholder: "Enter your sector",
                                },
                                dimensions: {
                                  width: "450px",
                                },
                              },
                              type: "custom",
                            },
                          })
                        );
                      }}
                    >
                      Can't find your sector
                    </MenuItem>
                  </Select>


                  {errors[index]?.sector && (
                    <Box sx={{ color: "error.main", fontSize: "0.75rem", mt: 1 }}>
                      {errors[index].sector}
                    </Box>
                  )}
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel id="" sx={{ color: "#8E9091", fontWeight: 500 }}>
                    Type of Work
                  </InputLabel>
                  <Select
                    size="small"
                    id="Country"
                    name={`type`}
                    displayEmpty
                    defaultValue=""
                    value={WorkExperienceMember.type}
                    onChange={(e: any) =>
                      eventHandler({
                        name: "inputChange",
                        data: { index, e },
                      })
                    }
                    IconComponent={ExpandMoreIcon}
                    required
                    fullWidth
                    error={!!errors[index]?.type}
                    MenuProps={MenuProps}
                  >
                    <MenuItem  sx={{color:"#495052"}} disabled value="">
                      Select
                    </MenuItem>
                    {workTypes.map((type) => (
                      <MenuItem  sx={{color:"#495052"}} key={type.name} value={type.value}>
                        {type.name}
                      </MenuItem>
                    ))}
                  </Select>
                  {errors[index]?.type && (
                    <Box sx={{ color: "error.main", fontSize: "0.75rem", mt: 1 }}>
                      {errors[index].type}
                    </Box>
                  )}
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel id="" sx={{ color: "#8E9091", fontWeight: 500 }}>
                    Designation
                  </InputLabel>
                  <TextField
                    size="small"
                    name={`designation`}
                    placeholder="Designation"
                    value={WorkExperienceMember.designation}
                    onChange={(e: any) =>
                      eventHandler({
                        name: "inputChange",
                        data: { index, e },
                      })
                    }
                    required
                    fullWidth
                    error={!!errors[index]?.designation}
                    helperText={errors[index]?.designation}
                  />
                  
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel id="" sx={{ color: "#8E9091", fontWeight: 500 }}>
                    Status
                  </InputLabel>
                  <FormControl sx={{}}>
                    <RadioGroup
                      row
                      name={`workStatus`}
                      value={workStatus[index]}
                      onChange={(e: any) =>
                        eventHandler({
                          name: "statusChange",
                          data: { index, e },
                        })
                      }
                    >
                      <FormControlLabel
                       sx={{color:"#495052"}}
                        value="completed"
                        control={<Radio />}
                        label="Completed"
                      />
                      <FormControlLabel
                       sx={{color:"#495052"}}
                        value="ongoing"
                        control={<Radio />}
                        label="Ongoing"
                      />
                    </RadioGroup>
                  </FormControl>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel id="" sx={{ color: "#8E9091", fontWeight: 500 }}>
                    Upload Document
                  </InputLabel>
                  <Stack
                    direction="row"
                    spacing={1}
                    alignItems="center"
                    onClick={() =>
                      inputFile?.current?.click()
                    }
                  >
                    {WorkExperienceMember.docId ? "" :
                      <>
                        <Button
                          variant="outlined"
                          size="small"
                          component="label"
                          startIcon={<FileUploadOutlinedIcon />}
                          sx={{
                            ...secondaryButton,
                            fontSize: "12px",
                            textTransform: "capitalize",
                            color:"#495052"
                          }}
                        >
                          Upload
                          <input
                            type="file"
                            hidden
                            onChange={(e: any) =>
                              eventHandler({
                                name: "documentUpload",
                                data: { index, e },
                              })
                            }
                          />
                        </Button>
                        <Tooltip
                          slotProps={{
                            tooltip: {
                              sx: {
                                color: "#000",
                                backgroundColor: "#fff",
                                fontSize: "14px",
                                boxShadow: 1,
                                "&.MuiTooltip-tooltip":
                                {
                                  marginTop: "3px !important",
                                },
                              },
                            },
                          }}
                          title="Only jpeg , png , pdf files are accepted"
                          placement="bottom"
                        >
                          <Info sx={{ color: "#7d7d7d", fontSize: "1rem", cursor: "pointer" }} />
                        </Tooltip>
                      </>}
                    {loading ? (
                      <Typography variant="body2" color="textSecondary">
                        Uploading...
                      </Typography>
                    ) : (
                      WorkExperienceMember.docId ?
                        <Typography variant="body2" color="textSecondary" sx={{ display: "flex", gap: "1rem", alignItems: "center" }} >
                          {WorkExperienceMember?.docId?.data?.originalname}
                          <VisibilityOutlinedIcon sx={{ fontSize: "large" }} onClick={() => {
                            dispatch(
                              setPopup({
                                show: true,
                                data: {
                                  container: {
                                    name: "fileViewer",
                                    data: WorkExperienceMember.docId?.data?.preview_url,
                                    dimensions: {
                                      width: "500px",
                                      // height: "35%",
                                      // close: "none",
                                      // show: "none"
                                    },
                                  },
                                  type: "custom",
                                },
                              })
                            )
                          }} />
                        </Typography>
                        : ""
                    )}
                  </Stack>
                  {errors[index]?.docId && (
                    <Box sx={{ color: "error.main", fontSize: "0.75rem", mt: 1 }}>
                      {errors[index].docId}
                    </Box>
                  )}
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel id="" sx={{ color: "#8E9091", fontWeight: 500 }}>
                    Start Date
                  </InputLabel>
                  <DatePickerComponent
                        selectedDate={WorkExperienceMember?.startDate}
                        onChange={(date: Date | null) =>
                          eventHandler({
                            name: "inputChange",
                            data: {
                              index,
                              e: {
                                target: {
                                  name: "startDate",
                                  value: date ? date.toISOString().slice(0, 10) : "",
                                },
                              },
                            },
                          })
                        }
                        maxDate={new Date()}
                        placeholderText="Start Date"
                      />
                      {errors[index]?.startDate && (
                    <Box sx={{ color: "error.main", fontSize: "0.75rem", mt: 1 }}>
                      {errors[index].startDate}
                    </Box>
                  )}
                </Grid>
                {workStatus[index] === "completed" && (
                  <Grid item xs={12} sm={6}>
                    <InputLabel id="" sx={{ color: "#8E9091", fontWeight: 500 }}>
                      End Date
                    </InputLabel>
                    <DatePickerComponent
                        selectedDate={WorkExperienceMember?.endDate}
                        onChange={(date: Date | null) =>
                          eventHandler({
                            name: "inputChange",
                            data: {
                              index,
                              e: {
                                target: {
                                  name: "endDate",
                                  value: date ? date.toISOString().slice(0, 10) : "",
                                },
                              },
                            },
                          })
                        }
                        minDate={
                          WorkExperienceMember?.startDate
                            ? new Date(
                              new Date(WorkExperienceMember?.startDate).getTime() + 10 * 24 * 60 * 60 * 1000
                            )
                            : undefined
                        } 
                        maxDate={new Date()}
                        placeholderText="End Date"
                      />
                      {errors[index]?.endDate && (
                    <Box sx={{ color: "error.main", fontSize: "0.75rem", mt: 1 }}>
                      {errors[index].endDate}
                    </Box>
                  )}
                  </Grid>
                )}

                {workExperienceData?.data?.length  > 0 && (
                  <Grid item xs={12} sx={{ mt: 3 }}>
                    <Box sx={{ display: "flex", justifyContent: "end" }}>
                      <img
                        src={Images.deleteIcon}
                        alt="delete"
                        style={{ width: "1.5rem",cursor:"pointer" }}
                        onClick={()=>handleDeleteOpen(index)}
                      />
                    </Box>
                  </Grid>
                )}
              </Grid>
            </div>
          ))}
          <Box sx={{ mt: 4 }}>
            <Stack
              direction="row"
              sx={{ mt: 5, mb: 1, display: "flex", justifyContent: "end", mr: 10 }}
              spacing={2}
            >
              {workExperienceData?.data?.length  > 0 && 
              <Button
                variant="outlined"
                sx={{
                  ...secondaryButton,
                  textTransform: "capitalize",
                  // fontSize: "12px",
                }}
                onClick={() =>
                  eventHandler({
                    name: "addWork",
                    data: {},
                  })
                }
              >
                Add Previous
              </Button>}
            
              <CustomButton
                // label="Submit"
                // variant="contained"
                width="100px"
                handleSubmit={() =>
                  eventHandler({
                    name: "submitForm",
                    data: {},
                  })
                }
              >
                  {load ? (
                              <img
                                src={Images.standardLoader}
                                alt="load"
                                style={{ width: "1rem",height:"1rem",padding:"4px" }}
                              />
                            ) : (
                              "Save"
                            )}
              </CustomButton>
            </Stack>
          </Box>
          <ConfirmDialog
            open={open}
            handleClose={() => setOpen(false)}
            additionalData={{
              onSubmit: () => eventHandler({
                  name: "removeWorkExperience",
                  data: deleteItem,
                }),
              title: "Are you sure you want to delete ?",
              content:
                "",
            }}
          />
        </Box>
      ) : (
        <Box sx={{ boxShadow: 2, borderRadius: 3, background: '#fff', p: 3, mb: 3 }}>
          <Grid container spacing={2}>
            <Grid item xs={12} sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
              <Typography variant="h6" sx={{ fontWeight: 600,color:"#8E9091" }}>
                Work Experience
              </Typography>
              <Button variant="outlined" sx={[{ border:'2px solid #F48438 ', borderRadius:'20px',color:'#495052' ,fontSize: { lg: "16px", xs: "12px" }, width: { lg: "100px", xs: "90px" }, padding: { lg: "8px", xs: "6px" } }]} onClick={() => setFormEdit(true)}>
                Edit
              </Button>
            </Grid>
            {WorkExperienceDetails && WorkExperienceDetails.length > 0 ? (
              <>
                <Grid item xs={12}>
                  <Typography sx={{ color: 'grey', fontWeight: 500 }}>
                    Total Work Experience in months: {getTotalWorkExperienceInMonths() > 0 ? getTotalWorkExperienceInMonths() : 'NA'}
                  </Typography>
                </Grid>
                {/* Table Header */}
                <Grid item xs={12}>
                  <Grid container sx={{ fontWeight: 600, background: '#f5f5f5', borderRadius: 1, py: 1 }}>
                    <Grid item xs={12} sm={3} md={2}><Typography sx={{color:"#8E9091"}}>Name of Organization</Typography></Grid>
                    <Grid item xs={12} sm={2} md={1.5}><Typography sx={{color:"#8E9091"}}>Sector</Typography></Grid>
                    <Grid item xs={12} sm={2} md={1.5}><Typography sx={{color:"#8E9091"}}>Type of Work</Typography></Grid>
                    <Grid item xs={12} sm={2} md={1.5}><Typography sx={{color:"#8E9091"}}>Designation</Typography></Grid>
                    <Grid item xs={12} sm={1} md={1}><Typography sx={{color:"#8E9091"}}>Status</Typography></Grid>
                    <Grid item xs={12} sm={2} md={1.5}><Typography sx={{color:"#8E9091"}}>Start Date</Typography></Grid>
                    <Grid item xs={12} sm={2} md={1.5}><Typography sx={{color:"#8E9091"}}>End Date</Typography></Grid>
                    <Grid item xs={12} sm={2} md={1.5}><Typography sx={{color:"#8E9091"}}>Document</Typography></Grid>
                  </Grid>
                </Grid>
                {/* Table Rows */}
                {WorkExperienceDetails.map((exp, idx) => (
                  <Grid item xs={12} key={idx}>
                    <Grid container sx={{ py: 1, borderBottom: idx < WorkExperienceDetails.length - 1 ? '1px solid #eee' : 'none' }}>
                      <Grid item xs={12} sm={3} md={2}><Typography sx={{color:"#495052"}}>{exp.companyName || 'NA'}</Typography></Grid>
                      <Grid item xs={12} sm={2} md={1.5}><Typography sx={{color:"#495052"}}>{exp.sector || 'NA'}</Typography></Grid>
                      <Grid item xs={12} sm={2} md={1.5}><Typography sx={{color:"#495052"}}>{exp.type || 'NA'}</Typography></Grid>
                      <Grid item xs={12} sm={2} md={1.5}><Typography sx={{color:"#495052"}}>{exp.designation || 'NA'}</Typography></Grid>
                      <Grid item xs={12} sm={1} md={1}><Typography sx={{color:"#495052"}}>{exp.endDate ? 'Completed' : 'Ongoing'}</Typography></Grid>
                      <Grid item xs={12} sm={2} md={1.5}><Typography sx={{color:"#495052"}}>{exp.startDate ? exp.startDate.split('T')[0] : 'NA'}</Typography></Grid>
                      <Grid item xs={12} sm={2} md={1.5}><Typography sx={{color:"#495052"}}>{exp.endDate ? exp.endDate.split('T')[0] : 'NA'}</Typography></Grid>
                      <Grid item xs={12} sm={2} md={1.5}><Typography sx={{color:"#495052"}}>{exp.docId?.data?.originalname || 'NA'}</Typography></Grid>
                    </Grid>
                  </Grid>
                ))}
              </>
            ) : (
              <Grid item xs={12}><Typography>No work experience added.</Typography></Grid>
            )}
          </Grid>
        </Box>
      )}
    </>
  );
};

export default WorkExperiences;
