import {
  Card,
  CardHeader,
  CardContent,
  Collapse,
  IconButton,
} from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import ExpandLessIcon from "@mui/icons-material/ExpandLess";

interface ExpandCardProps {
  title: string;
  children: React.ReactNode;
  isOpen: boolean;
  onToggle: () => void;
}

export default function Accordion({ title, children, isOpen, onToggle }: ExpandCardProps) {
  return (
    <Card sx={{ boxShadow: 0, p: 0, borderRadius: "8px", overflow: "hidden", marginBottom: 1 }}>
      <CardHeader
        onClick={onToggle}
        sx={{
          padding: "8px !important",
          background: "#F7FAFD",
          color: "#495052",
          cursor: "pointer",
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          ".MuiTypography-root": {
            fontSize: "1rem",
            fontWeight: 500,
          },
        }}
        title={title}
        action={
          <IconButton
            onClick={(e) => {
              e.stopPropagation();
              onToggle();
            }}
            aria-label="expand"
            size="small"
            sx={{ marginRight: "12px" }}
          >
            {isOpen ? <ExpandLessIcon /> : <ExpandMoreIcon />}
          </IconButton>
        }
      />
      <Collapse in={isOpen} timeout="auto" unmountOnExit>
        <CardContent sx={{ boxShadow: 3, p: 2 }}>{children}</CardContent>
      </Collapse>
    </Card>
  );
}
