import {
  Box,
  CircularProgress,
  FormControl,
  FormControlLabel,
  Grid,
  InputLabel,
  MenuItem,
  Radio,
  RadioGroup,
  Select,
  TextField,
  Tooltip,
  Typography,
  Button,
  ListSubheader,
  InputAdornment
} from "@mui/material";
import { ErrorMessage, Form, Formik } from "formik";
import React, { useEffect, useMemo, useState } from "react";
import CustomField from "../../../../genericComponents/customTextfield";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { pgCourseList } from "../../../../assets/menu";
import { useAppDispatch, useAppSelector } from "../../../../assets/hooks";
import { setPostGraduation } from "../../../../store/Slices/educationHistorySlice";
import { editProfile, getSearchUniversityDetails } from "../../../../services";
import { setPopup } from "../../../../store/Slices/popupSlice";
import { Country, State, City } from "country-state-city";
import { Event } from "../../../../types/types";
import { CustomButton } from "../../../../genericComponents/customButton";
import * as Yup from "yup";
import { Close, Star } from "@mui/icons-material";
import InfoIcon from "@mui/icons-material/Info";
import ReusableDropdown from "src/genericComponents/reusableSelect";
import Images from "src/assets";
import { globalButton } from "src/components/styles/customStyles";
import SearchIcon from "@mui/icons-material/Search";
import DatePickerComponent from "src/genericComponents/datePicker";



interface ICountry {
  name: string;
  isoCode: string;
}

interface IState {
  name: string;
  isoCode: string;
  countryCode: string;
}

interface ICity {
  name: string;
  stateCode: string;
  countryCode: string;
}
const PostGrad = ({ data,profileData }: any) => {
  const dispatch = useAppDispatch();
  const [countries, setCountries] = useState<ICountry[]>([]);
  const [states, setStates] = useState<IState[]>([]);
  const [cities, setCities] = useState<ICity[]>([]);
  const [institutions, setInstitutions] = useState([])
  const [institutionName, setInstitutionName] = useState<string>(data?.instituteName || "");
  const [postGradData] = useState(profileData?.postGraduation || {});
  console.log(postGradData)
  const [universities, setUniversities] = useState<string[]>(data?.affiliatedUniversity ? [data.affiliatedUniversity] : []);
  const [affiliatedUniversity, setAffiliatedUniversity] = useState<string>(data?.affiliatedUniversity || "");
  const [programMajor, setProgramMAjor] = useState<string>(data?.specialization || "");
  const [degreeName, setDegreeName] = useState<string>(data?.degreeProgram ? data?.degreeProgram : "");


  const [ieh, setIeh] = useState<boolean>(false);
  const [institutionId, setInstitutionId] = useState<string>("");
  const [gradingSystem, setGradingSystem] = useState(data?.gradingSystem);
  const [totalScore, setTotalScore] = useState(data?.totalScore);
  const profile = useAppSelector((store) => store.profile);
  const [iehStatus, setIEHStatus] = useState<any>(profile?.data?.IEH);
  const [hasSearched, setHasSearched] = useState(false);
  const [loader, setLoader] = useState(false);

  const handleGradingSystemChange = (event: React.ChangeEvent<{ value: unknown }>) => {
    // const { name, value } = event.target;
    // const updatedData = { ...postGradData, [name]: value };
    // setPostGradData(updatedData);
    setGradingSystem(event.target.value as string);
    setTotalScore('');
  };
  const [selectedCountry, setSelectedCountry] = useState(
    data?.country || "India"
  );
  const [selectedState, setSelectedState] = useState(
    data?.state
  );
  const [selectedCity, setSelectedCity] = useState(
    data?.city
  );
  const [loading, setLoading] = useState<boolean>(false);
  const [searchTerm, setSearchTerm] = useState<string>("");

  const countryInfo = countries.find((c: any) => c.name === selectedCountry);
  const stateInfo = states.find((s: any) => s.name === selectedState);

  useEffect(() => {
    const fetchCountries = async () => {
      const allCountries = await Country.getAllCountries();
      setCountries(allCountries);
    };

    fetchCountries();
  }, []);

  const profileSchema = Yup.object().shape({
    postGraduation: Yup.object().shape({
      // degreeProgram: Yup.string().required("Degree Program is required"),
      // programMajor: Yup.string().required("Specialization is required"),
      // instituteName: Yup.string().required("Institution Name is required"),
      // affiliatedUniversity: Yup.string().required("Affiliated University is required"),
      // gradingSystem: Yup.string().required("Grading System is required"),
      // totalScore: Yup.mixed() 
      //   .required('Score is required')
      //   .test('valid-score', 'Invalid score', function (value) {
      //     const { gradingSystem } = this.parent;
      //     if (gradingSystem === 'percentage') {
      //       return typeof value === 'number' && value >= 1 && value <= 100;
      //     }
      //     if (gradingSystem === 'GPA') {
      //       return typeof value === 'number' && value >= 1 && value <= 10;
      //     }
      //     if (gradingSystem === 'CGPA') {
      //       return typeof value === 'number' && value >= 1 && value <= 6;
      //     }

      //     if (gradingSystem === 'grade') {
      //       return typeof value === 'string' && ['A', 'A+', 'B', 'B+', 'C', 'D'].includes(value);
      //     }

      //     return true; 
      //   })
      // ,
      startDate: Yup.date().required("Start Date is required"),
      endDate: Yup.date()
        .when("isCompleted", {
          is: true,
          then: schema => schema.required("End Date is required"),
        })
        .nullable()
        .test('not-same-date', 'Start date and end date cannot be the same.', function (endDate) {
          const { startDate } = this.parent;
          if (!startDate || !endDate) return true;
          const start = new Date(startDate);
          const end = new Date(endDate);
          return start.getFullYear() !== end.getFullYear() || start.getMonth() !== end.getMonth() || start.getDate() !== end.getDate();
        }),
      // backlogs: Yup.number()
      //   .when("backlogs", {
      //     is: (value: string) => value === "yes",
      //     then: schema => schema.required("Number of Backlogs is required").min(1),
      //   })
      //   .nullable(),
      // country: Yup.string().required("Country is required"),
      // state: Yup.string().required("State/Province is required"),
      // city: Yup.string().required("City/Town is required"),
    }),
  });
  useEffect(() => {
    const fetchStateAndCities = async () => {
      const statesOfCountry = await State.getStatesOfCountry(
        countryInfo?.isoCode
      );
      setStates(statesOfCountry);

      const citiesOfState = await City.getCitiesOfState(
        stateInfo?.countryCode ?? "",
        stateInfo?.isoCode ?? ""
      );
      setCities(citiesOfState);
    };
    fetchStateAndCities();
  }, [countryInfo, stateInfo]);

  const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value;
    setSearchTerm(value);
    setInstitutionName(value);
  };

  // Handle institution selection

  useEffect(() => {
    if (searchTerm.length > 3) {
      setLoading(true);
      setHasSearched(true); // Mark as searched when making an API call
      getSearchUniversityDetails(searchTerm,1,10, 1, 0, 0, 0,0)
        .then((response:any) => {
          setInstitutions(response.data?.institutions || []);
          setLoading(false);
        })
        .catch((error:any) => {
          console.error("Error fetching institutions:", error);
          setLoading(false);
        });
    } else {
      setInstitutions([]);
      setHasSearched(false); // Reset search state if searchTerm is not long enough
    }
  }, [searchTerm]);

  const [customField, setCustomField] = useState<boolean>(false);

  const ITEM_HEIGHT = 48;
  const ITEM_PADDING_TOP = 8;
  const MenuProps = {
    PaperProps: {
      style: {
        maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
        width: 150,
      },
    },
  };
  const initialValues = {
    postGraduation: {
      instituteName: institutionName,
      city: selectedCity,
      state: selectedState,
      country: selectedCountry,
      specialization: programMajor, // enum  eee,ese,ece
      degreeProgram: degreeName, // enum btech,bedu,bsc....
      gradingSystem: gradingSystem, // enum % grade gpa cgpa
      affiliatedUniversity: affiliatedUniversity,
      totalScore: totalScore, // for grade A+..., for Percent 0-100, gpa 0-10
      startDate: data?.startDate?data?.startDate:new Date().toISOString(),
      endDate: data?.endDate?data.endDate:new Date().toISOString(),
      backlogs: data?.backlogs,
      isCompleted: data?.isCompleted,
      custom: customField
    },
  };
  console.log("post",initialValues);
  const [specializations, setSpecializations] = useState<any>([]);

  const handleDegreeChange = (event: React.ChangeEvent<{ value: unknown }>, setFieldValue: Function) => {
    const selectedDegree = event.target.value as string;
    // setFieldValue('postGraduation.degreeProgram', selectedDegree);
    const degreeCourses = pgCourseList[0] as { [key: string]: string[] };
    const selectedSpecializations = degreeCourses[selectedDegree] || [];
    setSpecializations(selectedSpecializations);
    setFieldValue('postGraduation.programMajor', '');
  };

  useEffect(() => {
    const degreeCourses = pgCourseList[0] as { [key: string]: string[] };
    const selectedSpecializations = degreeCourses[data?.degreeProgram] || [];
    setSpecializations(selectedSpecializations);
  }, [data?.degreeProgram])

  const handleOptionClick = (option: any) => {
    if (option === "Other") {
      // setShowCustomInput(true);
      setInstitutionName("");
      setIeh(false);
      setSearchTerm("");
      setCustomField(true)
      dispatch(
        setPopup({
          show: true,
          data: {
            container: {
              name: "customfield",
              data: {
                name: "postGraduation.institutionName",
                fieldName: institutionName,
                setFieldName: setInstitutionName,
                placeholder: "Enter your institution name"
              },
              dimensions: {
                width: "450px",
              },
            },
            type: "custom",
          },
        })
      )
    } else {
      setInstitutionName(option?.InstitutionName);
      setUniversities(option?.university || []);
      setIeh(option?.IEH?.exists)
      setInstitutionId(option?._id)
      setAffiliatedUniversity("");
      setSearchTerm("");
    }
  };
  const [searchQuery, setSearchQuery] = useState("");

  const filteredCities = useMemo(
    () =>
      cities.filter((city) =>
        city.name.toLowerCase().includes(searchQuery.toLowerCase())
      ),
    [cities, searchQuery]
  );
  const eventHandler = async (event: Event) => {
    switch (event.name) {
      case "Country":
        setSelectedCountry(event.data);
        setSelectedState('');
        setSelectedCity('');
        setCities([]);

        const countryInfo = countries.find((c: any) => c.name === event.data);
        if (countryInfo) {
          const statesOfCountry = await State.getStatesOfCountry(countryInfo.isoCode);
          setStates(statesOfCountry);
        }
        break;

      case "State":
        setSelectedCity('');
        setSelectedState(event.data);
        const stateInfo = states.find((s: any) => s.name === event.data);
        if (stateInfo) {
          const citiesOfState = await City.getCitiesOfState(stateInfo.countryCode, stateInfo.isoCode);
          setCities(citiesOfState);
        }
        break;

      case "City":
        setSelectedCity(event.data);
        break;
      default:
        break;

    }
  };

  const submitHandler = async (values: any) => {
    try {

      setLoader(true)
      const response = await editProfile(values);
      if (response) {
        dispatch(setPostGraduation(response.data.data.education.postGraduation));
        const updatedData = response.data.data.education.postGraduation;
        // Update local state if needed (already handled by useState hooks)
        // Update data prop fields for read-only view
        data.startDate = updatedData.startDate;
        data.endDate = updatedData.endDate;
        data.isCompleted = updatedData.isCompleted;
        data.affiliatedUniversity = updatedData.affiliatedUniversity;
        data.specialization = updatedData.specialization;
        data.degreeProgram = updatedData.degreeProgram;
        data.gradingSystem = updatedData.gradingSystem;
        data.totalScore = updatedData.totalScore;
        data.backlogs = updatedData.backlogs;
        data.country = updatedData.country;
        data.state = updatedData.state;
        data.city = updatedData.city;
        data.instituteName = updatedData.instituteName;
        setLoader(false)
        dispatch(
          setPopup({
            show: true,
            data: {
              container: {
                name: "success",
                data: "Post graduate Details are added succesfully",
                dimensions: {
                  width: "35%",
                  height: "40%",
                  show: "none"
                },
              },
              type: "custom",
            },
          })
        );
      }
    } catch (error) {
      setLoader(false)
    }
  };

  console.log("Masters",data);

  const [formEdit, setFormEdit] = useState(false);

  return (
    <>
      {formEdit ? (
        <div>
          <Formik
            initialValues={initialValues}
            validationSchema={profileSchema}
            enableReinitialize
            onSubmit={(values: any) => submitHandler(values)}
          >
            {({ touched, errors, values, handleChange, setFieldValue }) => {
              // console.log(errors)
              return (
                <Form style={{}}>
                  <Grid container spacing={3} sx={{ px: 2 }}>
                    <Grid item xs={12} sm={6}>
                      <InputLabel
                        id="instituteName"
                        sx={{ fontWeight: "500", color: "#8E9091", mb: 1 }}
                      >
                        Degree Name
                      </InputLabel>
                      <Select
                        size="small"
                        id="Country"
                        name="postGraduation.degreeProgram"
                        displayEmpty
                        defaultValue=""
                        value={degreeName}
                        onChange={(e: any) => {
                          handleDegreeChange(e, setFieldValue);
                          setDegreeName(e.target.value);
                        }}
                        fullWidth
                        sx={{
                          "& .Mui-error .MuiOutlinedInput-notchedOutline": {
                            borderColor: "red !important", // Custom error border color
                          },
                        }}
                        MenuProps={MenuProps}
                        // error={errors.postGraduation.programMajor}
                        IconComponent={ExpandMoreIcon}
                      >
                        <MenuItem
                          disabled
                          value=""
                          sx={{
                            "& .MuiTypography-root": {
                              fontSize: "14px !important",
                            },
                          }}
                        >
                          Select
                        </MenuItem>

                        {Object.keys(pgCourseList[0]).map((degree) => (
                          <MenuItem key={degree} value={degree} sx={{
                            "& .MuiTypography-root": {
                              fontSize: "14px !important",
                            },
                            "& .MuiSvgIcon-root ": {
                              fontSize: "1rem",
                            },
                            whiteSpace: "normal",
                          }}>
                            {degree}
                          </MenuItem>
                        ))}
                        {!Object.keys(pgCourseList[0]).includes(degreeName) && degreeName !== "" && (
                          <MenuItem key={degreeName} value={degreeName}>
                            {degreeName}
                          </MenuItem>
                        )}
                        <MenuItem
                          sx={{
                            fontSize: "14px !important",
                            display: "flex",
                            alignItems: "center",
                            whiteSpace: "normal",
                            color: "#1cbeb5  !important"
                          }}
                          onClick={() => {
                            setCustomField(true)
                            // setShowDegree(true)
                            dispatch(
                              setPopup({
                                show: true,
                                data: {
                                  container: {
                                    name: "customfield",
                                    data: {
                                      name: "postGraduation.degreeProgram",
                                      fieldName: degreeName,
                                      setFieldName: setDegreeName,
                                      placeholder: "Enter your degree name"
                                    },
                                    dimensions: {
                                      width: "450px",
                                    },
                                  },
                                  type: "custom",
                                },
                              })
                            )
                          }}
                        >
                          Can't find your degree
                        </MenuItem>
                      </Select>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <InputLabel
                        id="instituteName"
                        sx={{ fontWeight: "500", color: "#8E9091", mb: 1 }}
                      >
                        Specialization
                      </InputLabel>
                      <Select
                        size="small"
                        id="Country"
                        name="postGraduation.specialization"
                        displayEmpty
                        defaultValue=""
                        value={programMajor}
                        onChange={(e) => setProgramMAjor(e.target.value)}
                        fullWidth
                        sx={{
                          "& .Mui-error .MuiOutlinedInput-notchedOutline": {
                            borderColor: "red !important", // Custom error border color
                          },
                        }}
                        MenuProps={MenuProps}
                        // error={errors.postGraduation.programMajor}
                        IconComponent={ExpandMoreIcon}
                      >
                        <MenuItem
                          disabled
                          value=""
                          sx={{
                            "& .MuiTypography-root": {
                              fontSize: "14px !important",
                            },
                          }}
                        >
                          Select
                        </MenuItem>

                        {specializations?.map((name: string) => (
                          <MenuItem
                            key={name}
                            value={name}
                            sx={{
                              "& .MuiTypography-root": {
                                fontSize: "14px !important",
                              },
                              "& .MuiSvgIcon-root ": {
                                fontSize: "1rem",
                              },
                              whiteSpace: "normal",
                            }}
                          >
                            {name}
                          </MenuItem>
                        ))}
                        {!specializations.includes(programMajor) && programMajor !== "" && (
                          <MenuItem key={programMajor} value={programMajor}>
                            {programMajor}
                          </MenuItem>
                        )}
                        <MenuItem
                          sx={{
                            fontSize: "14px !important",
                            display: "flex",
                            alignItems: "center",
                            whiteSpace: "normal",
                            color: "#1cbeb5  !important"
                          }}
                          onClick={() => {
                            setCustomField(true)
                            dispatch(
                              setPopup({
                                show: true,
                                data: {
                                  container: {
                                    name: "customfield",
                                    data: {
                                      name: "postGraduation.specialization",
                                      fieldName: programMajor,
                                      setFieldName: setProgramMAjor,
                                      placeholder: "Enter your specialization"
                                    },
                                    dimensions: {
                                      width: "450px",
                                    },
                                  },
                                  type: "custom",
                                },
                              })
                            )
                          }}
                        >
                          Can't find your specialization
                        </MenuItem>
                      </Select>
                      <ErrorMessage
                        name="postGraduation.specialization"
                        component="div"
                        className="error"
                      />

                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <InputLabel id="instituteName" sx={{ fontWeight: "500", color: "#8E9091", mb: 1 }}>
                        Name of Institution
                      </InputLabel>
                      <Box sx={{ width: "100%", position: "relative" }}>
                        <TextField
                          value={institutionName}
                          onChange={handleInputChange}
                          placeholder="Search for an institution"
                          variant="outlined"
                          name="postGraduation.instituteName"
                          size="small"
                          autoComplete="off"
                          fullWidth
                          InputProps={{
                            endAdornment: (
                              <>
                                {loading && <CircularProgress color="inherit" size={20} />}
                                {institutionName && (
                                  <Close
                                    sx={{ cursor: "pointer" }}
                                    onClick={() => {
                                      setInstitutionName("");
                                      setInstitutions([]);
                                      setSearchTerm("");
                                      // setShowCustomInput(false);
                                      setHasSearched(false);
                                    }}
                                  />
                                )}
                              </>
                            ),
                          }}
                        />

                        {loading || institutions.length > 0 || (hasSearched && institutions.length === 0) ? (
                          <Box
                            sx={{
                              position: "absolute",
                              zIndex: 1,
                              backgroundColor: "#fff",
                              boxShadow: 1,
                              maxHeight: "200px",
                              overflowY: "auto",
                              width: "100%",
                            }}
                          >
                            {institutions.map((option: any, index) => (
                              <MenuItem
                                key={index}
                                sx={{
                                  fontSize: "14px !important",
                                  display: "flex",
                                  alignItems: "center",
                                  whiteSpace: "normal",
                                }}
                                onClick={() => handleOptionClick(option)}
                              >
                                {option?.InstitutionName}
                                {option?.IEH?.exists && <Star sx={{ fontSize: "1.1rem", color: "orange" }} />}
                                {option?.District && `, ${option?.District}`}
                              </MenuItem>
                            ))}
                            {!loading && hasSearched && (
                              <MenuItem
                                sx={{
                                  fontSize: "14px !important",
                                  display: "flex",
                                  // alignItems: "center",
                                  whiteSpace: "normal",
                                  color: "#1cbeb5  !important"

                                  // justifyContent: "center",
                                }}
                                onClick={() => handleOptionClick("Other")}

                              >
                                Can't find institution
                              </MenuItem>
                            )}
                          </Box>
                        ) : null}
                      </Box>

                      <ErrorMessage
                        name="postGraduation.instituteName"
                        component="div"
                        className="error"
                      />
                    </Grid>

                    <Grid item xs={12} sm={6}>
                      <InputLabel id="affiliatedUniversity" sx={{ fontWeight: "500", color: "#8E9091", mb: 1 }}>
                        Affiliated University
                      </InputLabel>
                      <Select
                        size="small"
                        id="affiliatedUniversity"
                        name="postGraduation.affiliatedUniversity"
                        displayEmpty
                        value={affiliatedUniversity}
                        onChange={(e) => setAffiliatedUniversity(e.target.value)}
                        fullWidth
                        IconComponent={ExpandMoreIcon}
                      >
                        <MenuItem disabled value="">
                          Select University
                        </MenuItem>
                        {universities.map((name: string) => (
                          <MenuItem key={name} value={name}>
                            {name}
                          </MenuItem>
                        ))}
                        {!universities.includes(affiliatedUniversity) && programMajor !== "" && (
                          <MenuItem key={affiliatedUniversity} value={affiliatedUniversity}>
                            {affiliatedUniversity}
                          </MenuItem>
                        )}
                        <MenuItem
                          sx={{
                            fontSize: "14px !important",
                            display: "flex",
                            alignItems: "center",
                            whiteSpace: "normal",
                            color: "#1cbeb5  !important"

                          }}
                          onClick={() => {
                            setCustomField(true)
                            dispatch(
                              setPopup({
                                show: true,
                                data: {
                                  container: {
                                    name: "customfield",
                                    data: {
                                      name: "postGraduation.affiliatedUniversity",
                                      fieldName: affiliatedUniversity,
                                      setFieldName: setAffiliatedUniversity,
                                      placeholder: "Enter your university"
                                    },
                                    dimensions: {
                                      width: "450px",
                                    },
                                  },
                                  type: "custom",
                                },
                              })
                            )
                          }}
                        >
                          Can't find university
                        </MenuItem>
                      </Select>

                      <ErrorMessage
                        name="postGraduation.affiliatedUniversity"
                        component="div"
                        className="error"
                      />
                    </Grid>
                    {ieh ?
                      <Grid item xs={12} sx={{ display: "flex", gap: "1rem", alignItems: "center" }}>
                        <Typography>This college is a member of <b style={{ color: "#3b3f76" }}>IEH</b> . You need to verify to get benefits.</Typography>
                        {iehStatus?.verificationStatus === "Verification Request Initiated" ? "" :
                          <Button sx={globalButton} onClick={() =>
                            dispatch(
                              setPopup({
                                show: true,
                                data: {
                                  container: {
                                    name: "ieh",
                                    data: {
                                      id: institutionId,
                                      setIEHStatus: setIEHStatus
                                    },
                                    dimensions: {
                                      width: "450px",
                                    },
                                  },
                                  type: "custom",
                                },
                              })
                            )
                          }>Verify</Button>}
                      </Grid>
                      : ""}

                    {iehStatus?.verificationStatus === "Verification Request Initiated" ?
                      <Grid item xs={12}>
                        <Typography>Your IEH verification request is initiated</Typography>
                      </Grid> : ""}

                    <Grid item xs={12} sm={6}>
                      <InputLabel
                        id="instituteName"
                        sx={{ fontWeight: "500", color: "#8E9091", mb: 1 }}
                      >
                        Grading System
                      </InputLabel>

                      <Select
                        id="grading-system"
                        size="small"
                        name="gradingSystem"
                        displayEmpty
                        defaultValue=""

                        value={gradingSystem}
                        onChange={(e: any) => handleGradingSystemChange(e)}
                        fullWidth
                        IconComponent={ExpandMoreIcon}
                      >
                        <MenuItem value="" disabled>Select grade</MenuItem>
                        <MenuItem value="percentage">Percentage</MenuItem>
                        <MenuItem value="gpa">CGPA    4.0 scale</MenuItem>
                        <MenuItem value="cgpa">CGPA   10.0 scale</MenuItem>
                        <MenuItem value="grade">Grade</MenuItem>
                      </Select>
                      <ErrorMessage
                        name="postGraduation.gradingSystem"
                        component="div"
                        className="error"
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <InputLabel
                        id="instituteName"
                        sx={{ fontWeight: "500", color: "#8E9091", mb: 1 }}
                      >
                        Score{" "}
                      </InputLabel>
                      {gradingSystem === "grade" ?
                        <Select
                          size="small"
                          id="totalScore"
                          name="postGraduation.totalScore"
                          displayEmpty
                          value={totalScore}
                          onChange={(e) => setTotalScore(e.target.value)}
                          fullWidth
                          IconComponent={ExpandMoreIcon}
                          MenuProps={MenuProps}
                        >
                          <MenuItem disabled value="">
                            Select Grade
                          </MenuItem>
                          {["A", "A+", "B", "B+", "C", "C+", "D", "D+"].map((name: string) => (
                            <MenuItem key={name} value={name}>
                              {name}
                            </MenuItem>
                          ))}
                        </Select>
                        :
                        <TextField
                          // type="number"
                          name="postGraduation.totalScore"
                          size="small"
                          fullWidth
                          value={totalScore}
                          onChange={(e) => setTotalScore(e.target.value)}
                          placeholder={
                            gradingSystem === "percentage"
                              ? "Enter percentage"
                              : gradingSystem === "grade"
                                ? "Enter grade"
                                : "Enter score"
                          }
                          inputProps={{
                            step: "0.1",

                            min: gradingSystem === "percentage" ? 1 : gradingSystem === "gpa" ? 1 : gradingSystem === "cgpa" ? 1 : undefined,
                            max: gradingSystem === "percentage" ? 100 : gradingSystem === "gpa" ? 4 : gradingSystem === "cgpa" ? 10 : undefined,
                            list: gradingSystem === "grade" ? "grades" : undefined,
                            onInput: (e) => {
                              const target = e.target as HTMLInputElement;
                              const value = parseFloat(target.value);
                              if (gradingSystem !== "Grade") {
                                const min = gradingSystem === "percentage" ? 1 : gradingSystem === "gpa" ? 1 : gradingSystem === "cgpa" ? 1 : undefined;
                                const max = gradingSystem === "percentage" ? 100 : gradingSystem === "gpa" ? 4 : gradingSystem === "cgpa" ? 10 : undefined;

                                if (value < (min || 0) || value > (max || Infinity)) {
                                  target.value = value.toString().slice(0, -1); // Remove the last character
                                }
                              }
                            },
                          }}
                        />}

                      <ErrorMessage
                        name="postGraduation.totalScore"
                        component="div"
                        className="error"
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <InputLabel id="" sx={{ color: "#8E9091", fontWeight: 500 }}>
                        Education status
                      </InputLabel>
                      <FormControl>
                        <RadioGroup
                          row
                          aria-labelledby="education-status"
                          name="postGraduation.isCompleted"
                          value={values.postGraduation.isCompleted}
                          onChange={(event) => {
                            handleChange({
                              target: {
                                name: "postGraduation.isCompleted",
                                value: event.target.value === "true",
                              },
                            });
                          }}
                        >
                          <FormControlLabel
                            value="true"
                            control={<Radio size="small" sx={{ "&.MuiRadio-root.Mui-checked": { color: "#3B3F76 !important" } }} />}
                            label="Completed"
                          />
                          <FormControlLabel
                            value="false"
                            control={<Radio size="small" sx={{ "&.MuiRadio-root.Mui-checked": { color: "#3B3F76 !important" } }} />}
                            label="Pursuing"
                          />
                        </RadioGroup>
                      </FormControl>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <InputLabel
                        id="instituteName"
                        sx={{ fontWeight: "500", color: "#8E9091", mb: 1 }}
                      >
                        Start Date
                      </InputLabel>
                      <DatePickerComponent
                        selectedDate={values?.postGraduation?.startDate}
                        onChange={(date) => handleChange({ target: { name: `postGraduation.startDate`, value: date?.toISOString().slice(0, 10) } })}
                        maxDate={new Date()}
                        placeholderText="Start Date"
                      />
                      <ErrorMessage
                        name="postGraduation.startDate"
                        component="div"
                        className="error"
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      {(values.postGraduation.isCompleted === "true" || values.postGraduation.isCompleted === true ) && (
                        <>
                          <InputLabel sx={{ fontWeight: "500", color: "#8E9091", mb: 1 }}>
                            End Date
                          </InputLabel>
                            <DatePickerComponent
                            selectedDate={values?.postGraduation?.endDate}
                            onChange={(date) =>
                              handleChange({
                                target: {
                                  name: `postGraduation.endDate`,
                                  value: date?.toISOString().slice(0, 10),
                                },
                              })
                            }
                            minDate={
                              values.postGraduation.startDate
                                ? new Date(
                                    new Date(values.postGraduation.startDate).setFullYear(
                                      new Date(values.postGraduation.startDate).getFullYear() + 1
                                    )
                                  )
                                : undefined
                            }
                            maxDate={
                              values.postGraduation.startDate
                                ? (() => {
                                    const twelveYearsFromStartDate = new Date(
                                      new Date(values.postGraduation.startDate).setFullYear(
                                        new Date(values.postGraduation.startDate).getFullYear() + 12
                                      )
                                    );
                                    return twelveYearsFromStartDate > new Date() ? new Date() : twelveYearsFromStartDate;
                                  })()
                                : new Date()
                            }
                            placeholderText="End Date"
                          />
                          {touched.postGraduation?.endDate && typeof errors.postGraduation?.endDate === 'string' && (
                            <p style={{ color: "red", marginTop: "5px" }}>
                              {errors.postGraduation?.endDate}
                            </p>
                          )}
                        </>
                      )}
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <InputLabel id="" sx={{ color: "#8E9091", display: "flex", alignItems: "center",mb:1 }}>
                        Backlogs
                        <Tooltip
                          slotProps={{
                            tooltip: {
                              sx: {
                                color: "#514E6A",
                                backgroundColor: "#ffff",
                                boxShadow: 2,
                                cursor: "pointer",
                              },
                            },
                          }}
                          title={"Backlog refers to a failed subject or exam that you have not cleared in your first attempt"}
                          placement="right"
                        >
                          <InfoIcon sx={{ color: "#3B3F76", ml: 1, cursor: "pointer", fontSize: "1rem", float: "right" }} />
                        </Tooltip>
                      </InputLabel>
                      <CustomField
                            id="name"
                            type="number"
                            placeholder="Number of backlogs(if any)"
                            name="postGraduation.backlogs"
                            value={values.postGraduation.backlogs}
                            onChange={handleChange}
                            fullWidth
                          />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <InputLabel
                        id="instituteName"
                        sx={{ fontWeight: "500", color: "#8E9091", mb: 1 }}
                      >
                        Country
                      </InputLabel>
                      <ReusableDropdown
                        label="Country"
                        name="postGraduation.country"
                        options={countries.map(country => country.name)}
                        value={selectedCountry}
                        onChange={(e: any) => eventHandler({
                          name: "Country",
                          data: e?.target.value,
                        })}
                        placeholder="Select"
                        searchEnabled={true}
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <InputLabel
                        id="instituteName"
                        sx={{ fontWeight: "500", color: "#8E9091", mb: 1 }}
                      >
                        State/Province
                      </InputLabel>
                      <ReusableDropdown
                        label="State"
                        name="postGraduation.state"
                        options={states.map(state => state.name)}
                        value={selectedState}
                        onChange={(e: any) => eventHandler({
                          name: "State",
                          data: e?.target.value,
                        })}
                        placeholder="Select"
                        searchEnabled={true}
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <InputLabel
                        id="instituteName"
                        sx={{ fontWeight: "500", color: "#8E9091", mb: 1 }}
                      >
                        City/Town
                      </InputLabel>
                      <FormControl fullWidth>
                        <Select
                          id="city"
                          name="postGraduation.city"
                          value={selectedCity}
                          displayEmpty
                          size="small"
                          defaultValue=""
                          onChange={(e) => setSelectedCity(e.target.value)}
                          fullWidth
                          IconComponent={ExpandMoreIcon}
                          onClose={() => setSearchQuery("")} // Reset search when dropdown closes
                          MenuProps={{
                            PaperProps: {
                              sx: {
                                maxHeight: 300,
                                "&.MuiPaper-root": {
                                  boxShadow: 3,
                                  borderRadius: "15px !important",
                                },
                              },
                            },
                          }}
                        >
                          <ListSubheader>
                            <TextField
                              size="small"
                              autoFocus
                              placeholder="Search City..."
                              fullWidth
                              autoComplete="off"
                              // autoFocus="true"
                              value={searchQuery}
                              onChange={(e) => setSearchQuery(e.target.value)}
                              InputProps={{
                                startAdornment: (
                                  <InputAdornment position="start">
                                    <SearchIcon />
                                  </InputAdornment>
                                ),
                              }}
                              onKeyDown={(e) => {
                                if (e.key !== "Escape") {
                                  e.stopPropagation();
                                }
                              }}
                            />
                          </ListSubheader>

                          <MenuItem value="" disabled>
                            {!selectedState ? "Please select state" :
                              "Select city"}
                          </MenuItem>

                          {filteredCities.length > 0 ? (
                            filteredCities.map((city: any) => (
                              <MenuItem key={city.name} value={city.name}>
                                {city.name}
                              </MenuItem>
                            ))
                          ) : (
                            selectedState && <MenuItem disabled>No cities available</MenuItem>
                          )}

                          {!cities.some((city) => city.name === selectedCity) &&
                            selectedCity !== undefined &&
                            selectedCity !== "" && (
                              <MenuItem key={selectedCity} value={selectedCity}>
                                {selectedCity}
                              </MenuItem>
                            )}

                          <MenuItem
                            sx={{
                              fontSize: "14px !important",
                              display: "flex",
                              alignItems: "center",
                              whiteSpace: "normal",
                              color: "#1cbeb5 !important",
                            }}
                            onClick={() => {
                              dispatch(
                                setPopup({
                                  show: true,
                                  data: {
                                    container: {
                                      name: "customfield",
                                      data: {
                                        name: "postGraduation.city",
                                        fieldName: selectedCity,
                                        setFieldName: setSelectedCity,
                                        placeholder: "Enter your city",
                                      },
                                      dimensions: {
                                        width: "450px",
                                      },
                                    },
                                    type: "custom",
                                  },
                                })
                              );
                            }}
                          >
                            Can't find city
                          </MenuItem>
                        </Select>
                      </FormControl>
                    </Grid>

                    <Grid
                      item
                      xs={12}
                      sx={{ display: "flex", justifyContent: "end", my: 2, mr: 10 }}
                    >
                      <CustomButton
                        handleSubmit={() => submitHandler}
                        width="100px"
                      >
                        {loader ? (
                          <img
                            src={Images.standardLoader}
                            alt="load"
                            style={{ width: "1rem", height: "1rem" }}
                          />
                        ) : (
                          "Save"
                        )}
                      </CustomButton>
                    </Grid>
                  </Grid>
                </Form>
              );
            }}
          </Formik>
        </div>
      ) : (
        <Box sx={{ boxShadow: 2, borderRadius: 3, background: '#fff', p: 3 }}>
          <Grid container spacing={2}>
            <Grid item xs={12} sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
              <Typography variant="h6" sx={{ fontWeight: 600,color:"#495052" }}>
                Post Graduation Details
              </Typography>
              <Button variant="outlined"sx={[{ border:'2px solid #F48438 ', borderRadius:'20px',color:'#495052' ,fontSize: { lg: "16px", xs: "12px" }, width: { lg: "100px", xs: "90px" }, padding: { lg: "8px", xs: "6px" } }]} onClick={() => setFormEdit(true)}>
                Edit
              </Button>
            </Grid>
            <Grid item xs={12} sm={6}>
              <Typography sx={{ color: "#8E9091", fontWeight: 500 }}>Degree Name</Typography>
              <Typography sx={{color:"#495052"}}>{degreeName || 'NA'}</Typography>
              <Typography sx={{ color: "#8E9091", fontWeight: 500, mt: 2 }}>Specialization</Typography>
              <Typography sx={{color:"#495052"}}>{programMajor || 'NA'}</Typography>
              <Typography sx={{ color: "#8E9091", fontWeight: 500, mt: 2 }}>Institution Name</Typography>
              <Typography sx={{color:"#495052"}}>{institutionName || 'NA'}</Typography>
              <Typography sx={{ color: "#8E9091", fontWeight: 500, mt: 2 }}>Affiliated University</Typography>
              <Typography sx={{color:"#495052"}}>{affiliatedUniversity || 'NA'}</Typography>
              <Typography sx={{ color: "#8E9091", fontWeight: 500, mt: 2 }}>Grading System</Typography>
              <Typography sx={{color:"#495052"}}>{gradingSystem || 'NA'}</Typography>
              <Typography sx={{ color: "#8E9091", fontWeight: 500, mt: 2 }}>Score</Typography>
              <Typography sx={{color:"#495052"}}>{totalScore || 'NA'}</Typography>
              
            </Grid>
            <Grid item xs={12} sm={6}>
              <Typography sx={{ color: "#8E9091", fontWeight: 500 }}>Country</Typography>
              <Typography sx={{color:"#495052"}}>{selectedCountry || 'NA'}</Typography>
              <Typography sx={{ color: "#8E9091", fontWeight: 500, mt: 2 }}>State/Province</Typography>
              <Typography sx={{color:"#495052"}}>{selectedState || 'NA'}</Typography>
              <Typography sx={{ color: "#8E9091", fontWeight: 500, mt: 2 }}>City/Town</Typography>
              <Typography sx={{color:"#495052"}}>{selectedCity || 'NA'}</Typography>
              <Typography sx={{ color: "#8E9091", fontWeight: 500, mt: 2 }}>Start Date</Typography>
              <Typography sx={{color:"#495052"}}>{data?.startDate ? data.startDate.split('T')[0] : 'NA'}</Typography>
              <Typography sx={{ color: "#8E9091", fontWeight: 500, mt: 2 }}>End Date</Typography>
              <Typography sx={{color:"#495052"}}>{data?.endDate ? data.endDate.split('T')[0] : 'NA'}</Typography>
              <Typography sx={{ color: "#8E9091", fontWeight: 500, mt: 2 }}>Backlogs</Typography>
              <Typography sx={{color:"#495052"}}>{data?.backlogs || 'NA'}</Typography>
              <Typography sx={{ color: "#8E9091", fontWeight: 500, mt: 2 }}>Education Status</Typography>
              <Typography sx={{color:"#495052"}}>{data?.isCompleted === true || data?.isCompleted === 'true' ? 'Completed' : data?.isCompleted === false || data?.isCompleted === 'false' ? 'Pursuing' : 'NA'}</Typography>
            </Grid>
          </Grid>
        </Box>
      )}
    </>
  );
};


export default PostGrad;
