import { Box, Button, Grid,Tooltip, Typography } from "@mui/material";
import React, { useState } from "react";
import Images from "../../../../assets";
import { useAppDispatch, useAppSelector } from "../../../../assets/hooks";
import CustomCard from "../../../../genericComponents/customCard";
import ConfirmDialog from "../../../../genericComponents/modalPopup/confirmDialog";
import { addToShortList} from "../../../../services";
import { setPopup } from "../../../../store/Slices/popupSlice";
import {  setShortlisted } from "../../../../store/Slices/shortlistedCoursesSlice";
import {  Event, ShortlistedCourse } from "../../../../types/types";
import TimerIcon from "@mui/icons-material/Timer";
import ShoppingCartOutlinedIcon from '@mui/icons-material/ShoppingCartOutlined';
import { monthNames } from "../../../../assets/menu";
import { useNavigate } from "react-router-dom";
import CustomCardHOC from '../../../../genericComponents/CustomCardHOC';

const Shortlisted = () => {
  const navigate=useNavigate()
  const shortlistedCourses = useAppSelector(
    (state) => state.shortlistedcourses
  );
  const [open, setOpen] = useState(false);
  const [deleteId, setDeleteId] = useState("");
  const userAuthStatus = useAppSelector((state) => state.userAuthStatus);
  const dispatch = useAppDispatch();
  // const navigate = useNavigate();
  const handleDeleteOpen = (id: string) => {
    setOpen(true);
    setDeleteId(id);
  };
  const handleDelete = async () => {
    const values = {
      action: "pull",
      courseId: deleteId,
    };
   
    const response = await addToShortList(values);
    if (response.data.success) {
      dispatch(setShortlisted(response.data.data))
    }
      setOpen(false);
    
  };
  const eventHandler = async (event: Event) => {
    switch (event.name) {
      case "cart":
        try {
          if (
            userAuthStatus?.data?.isAuthorized &&
            userAuthStatus?.data?.isRegistered
          ) {
            dispatch(
              setPopup({
                show: true,
                data: {
                  container: {
                    name: "applynow",
                    data:event.data,
                    dimensions: {
                      width: "450px",
                    },
                  },
                  type: "custom",
                },
              })
            );
          } else {
            dispatch(
              setPopup({
                show: true,
                data: {
                  container: {
                    name: "login",
                    dimensions: {
                      width: "800px",
                  height:"70%",
                  show:"none"

                    },
                  },
                  type: "custom",
                },
              })
            );
          }
        } catch (error) {}
        break;
    }
  };

  return (
    <div>
      <Grid
        container
        spacing={3}
        sx={{
          overflowY: "scroll",  
        }}
      >
        {shortlistedCourses.data && shortlistedCourses?.data?.length > 0 ? (
          <>
            {shortlistedCourses.data?.map(
              (item: ShortlistedCourse, i: number) => (
                                <Grid item xs={12} sm={6} lg={5} sx={{paddingLeft:{xs:0,md:"1rem"}}} key={i}>
                  <CustomCardHOC
                      logoSrc={item.university.logoSrc}
                      universityName={item.university.name}
                      courseName={item.name}
                      countryName={item.university.location.country}
                      actions={
                        <>
                          <Box
                            sx={{border:"1px solid #1C1F40",borderRadius:"50%",width:"2rem",height:"2rem",display: "flex",justifyContent: "center",alignItems: "center",}}
                            onClick={() => eventHandler({ name: "cart", data: item })}
                          >
                            <ShoppingCartOutlinedIcon sx={{fontSize:"1.25rem",color:"#1C1F40"}} />
                          </Box>
                          <Box
                            component="img"
                            src={Images.deleteIcon}
                            alt="icon"
                            sx={{width:"2rem",height:"2rem" }}
                            onClick={() => handleDeleteOpen(item._id)}
                          />
                        </>
                      }
                    >
                      <Typography
                        component="span"
                        fontSize="0.8rem"
                        fontWeight="500"
                        sx={{ display: "flex", alignItems: "center", whiteSpace: "nowrap", mt: 1,color:'#5C6163' }}
                      >
                        <TimerIcon sx={{ fontSize: "1.25rem" }} />
                        &nbsp; Start date :
                        {item?.startDate
                          ?.map((item: any, index: number) => monthNames[item?.courseStartingMonth])
                          .join(", ")}
                      </Typography>
                    </CustomCardHOC>
                </Grid>
              )
            )}
          </>
        ) : (
          <Grid
            item
            xs={12}
            sx={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "center",
              height: "60vh",
              minHeight: "350px",
            }}
          >
            <img
              src={Images.Confused}
              alt="noData"
              style={{ width: "220px", height: "220px", marginBottom: "1.5rem" }}
            />
            <Typography
              sx={{
                mb: 1,
                fontSize: "1.6rem",
                fontWeight: 600,
                color: "#495052",
                textAlign: "center",
              }}
            >
              No Favourites Yet
            </Typography>
            <Typography
              sx={{
                mb: 3,
                fontSize: "1rem",
                color: "#8E9091",
                textAlign: "center",
              }}
            >
            Discover programs and save the ones that exite you!
            </Typography>
            <Button
              sx={{
                background: "#F48438",
                color: "#fff",
                borderRadius: "20px",
                px: 4,
                py: 1,
                fontWeight: 500,
                fontSize: "1rem",
                textTransform: "none",
                boxShadow: "none",
                "&:hover": {
                  background: "#F48438",
                },
              }}
              onClick={() => {
                const subDisUrl = `activeTab=${encodeURIComponent(JSON.stringify('courses'))}&FilterData=${encodeURIComponent(JSON.stringify([]))}&page=${JSON.stringify(
                  1
                )}&search=${encodeURIComponent(JSON.stringify(''))}`
                const subDisciUrl = window.btoa(subDisUrl);
                navigate(`/explore?${subDisciUrl}`);
              }}
            >
              Add products
            </Button>
          </Grid>
        )}
      </Grid>
      <ConfirmDialog
        open={open}
        handleClose={() => setOpen(false)}
        additionalData={{
          onSubmit: handleDelete, // Pass the function directly without curly braces
          title: "Are you sure you want to delete this from your shortlist?",
          content:
            "You're just a few steps away from applying to universities.",
        }}
      />
    </div>
  );
}

export default Shortlisted;
