import { Avatar, Box, Grid, Stack, Typography } from "@mui/material";
import React from "react";
import { useAppDispatch, useAppSelector } from "../../../../assets/hooks";
import { getAdvisorTitle, normalizeType } from "../../../../assets/library";
import { commonStackStyles, commonTypographyStyles } from "../../../../components/styles/customStyles";
import { setPopup } from "../../../../store/Slices/popupSlice";
import { User } from "../../../../types/types";

const Advisors = () => {
  const advisor = useAppSelector((state) => state.advisor);
  const dispatch = useAppDispatch();

  interface Advisor {
    _id: string;
    info: User;
    assignedCountries: string[],
  }
  return (
    <Box
      sx={{
        borderRadius: "20px",
        background: "#F4F4FF",
        p: 2.5,
        boxShadow: 0,
        height: '100%',
        width: '100%',
        display: 'flex',
        flexDirection: 'column',
      }}
    >
      <Typography fontWeight={600} fontSize="1.25rem" color="#3B3F76" mb={2}>
        Advisors
      </Typography>
      <Stack spacing={2.2} sx={{ flex: 1, overflowY: 'auto', pr: 0.5 }}>
        {advisor?.data && Array.isArray(advisor?.data) && advisor?.data?.length > 0 ? (
          advisor?.data?.map((advisor: Advisor, idx: number) => (
            <Box
              key={advisor._id}
              sx={{
                display: 'flex',
                alignItems: 'center',
                background: '#fff',
                borderRadius: '16px',
                p: 2,
                boxShadow: '0px 2px 8px 0px #E0E0F6',
                cursor: 'pointer',
                minHeight: 80,
              }}
              onClick={() =>
                dispatch(
                  setPopup({
                    show: true,
                    data: {
                      container: {
                        name: "advisor",
                        data: advisor,
                        dimensions: {
                          width: "1200px",
                          height: "85%"
                        },
                      },
                      type: "custom",
                    },
                  })
                )
              }
            >
              <Avatar
                src={advisor?.info?.displayPicSrc}
                alt="pic"
                sx={{ width: 28, height: 28, mr: 2, boxShadow: '0 1px 4px #E0E0F6' }}
              />
              <Box>
                <Typography fontWeight={700} color="#3B3F76" fontSize="1.05rem" lineHeight={1.3}>
                  {normalizeType(advisor?.info?.firstName)}
                </Typography>
                <Typography color="#3B3F76" fontSize="0.85rem" fontWeight={400} lineHeight={1.3}>
                  {getAdvisorTitle(advisor?.info?.role)}
                </Typography>
                <Box
                  sx={{
                    display: 'inline-block',
                    mt: 1,
                    px: 1.5,
                    py: 0.3,
                    borderRadius: '12px',
                    background: '#E6E6FA',
                  }}
                >
                  <Typography fontSize="0.65rem" color="#3B3F76" fontWeight={600}>
                    6+ Years experience
                  </Typography>
                </Box>
              </Box>
            </Box>
          ))
        ) : (
          <Typography color="#3B3F76" fontWeight={500} fontSize="1rem">
            Advisors not assigned
          </Typography>
        )}
      </Stack>
    </Box>
  );
};

export default Advisors; 